<!-- TeamSeeding.vue with Drag and Drop -->
<template>
  <div class="team-seeding">
    <!-- <h2>Team Seeding</h2> -->

    <div v-if="loading" class="loading">Loading teams...</div>

    <div v-else-if="error" class="error">{{ error }}</div>

    <div v-else>
      <!-- <div class="seeding-intro">
        <p>
          Teams are organized into seeding pots based on their estimated
          strength. These pots will be used to create balanced qualification
          groups.
        </p>
        <p v-if="isAdmin" class="drag-hint">
          <strong>Pro tip:</strong> Drag and drop teams from the unassigned pool
          into pots for easier assignment.
        </p>
      </div> -->

      <!-- Admin-only controls -->
      <div v-if="isAdmin" class="admin-controls">
        <div class="pot-controls">
          <h3>Seeding Management</h3>

          <div class="pot-config">
            <div class="form-group">
              <label for="num-pots">Number of Pots:</label>
              <input
                type="number"
                id="num-pots"
                v-model.number="numPots"
                min="1"
                max="10"
                @change="updatePots"
              />
            </div>

            <div class="auto-assign">
              <v-row>
                <button @click="autoAssignTeams" class="btn auto-btn">
                  Auto-Assign Teams to Pots
                </button>
                <button @click="resetTeamAssignment" class="btn reset-btn">
                  Reset Assignment
                </button>
              </v-row>
            </div>
          </div>
        </div>
      </div>

      <!-- Unassigned Teams Pool -->
      <div v-if="isAdmin" class="unassigned-teams-section">
        <div class="section-header">
          <h3>Unassigned Teams</h3>
          <span class="team-count">{{ unassignedTeams.length }} teams</span>
        </div>
        <div
          class="unassigned-teams-container"
          :class="{ 'drop-target': isDropTarget('unassigned') }"
          @dragover.prevent="onDragOver('unassigned')"
          @dragleave="onDragLeave"
          @drop="onDrop($event, 'unassigned')"
        >
          <div
            v-for="team in unassignedTeams"
            :key="team.id"
            class="team-item team-compact"
            :draggable="isAdmin"
            @dragstart="onDragStart($event, team, 'unassigned')"
            :class="{ dragging: draggedTeam?.id === team.id }"
            title="Drag to assign to a pot"
          >
            <span class="team-name-only">{{ team.name }}</span>
            <div class="team-tooltip">
              <div class="tooltip-content">
                <div v-if="team.company" class="tooltip-item">
                  <strong>Company:</strong> {{ team.company }}
                </div>
                <div v-if="team.preferredDay" class="tooltip-item">
                  <strong>Preferred Day:</strong> {{ team.preferredDay }}
                </div>
                <div
                  v-if="team.restrictedDays && team.restrictedDays.length"
                  class="tooltip-item"
                >
                  <strong>Only Available:</strong>
                  {{ team.restrictedDays.join(", ") }}
                </div>
                <div
                  v-if="team.members && team.members.length"
                  class="tooltip-item"
                >
                  <strong>Members:</strong>
                  <!-- {{ team.members.map((m) => m.firstName).join(", ") }} -->
                  {{
                    team.members
                      .map((m) =>
                        (m.firstName.trim() + " " + (m.lastName || "")).trim()
                      )
                      .join(", ")
                  }}
                </div>
                <!-- <div v-if="team.points" class="tooltip-item">
                  <strong>Points:</strong> {{ team.points }}
                </div> -->
              </div>
            </div>
          </div>
          <div
            v-if="isAdmin && unassignedTeams.length === 0"
            class="empty-message"
          >
            All teams have been assigned to pots
          </div>
        </div>
      </div>

      <!-- Seeding Pots -->
      <div class="seeding-pots-wrapper">
        <div class="seeding-pots">
          <div
            v-for="(pot, potIndex) in pots"
            :key="`pot-${potIndex}`"
            class="pot-container"
          >
            <div class="pot-header">
              <h3>Pot {{ potIndex + 1 }}</h3>
              <span class="team-count">{{ pot.teams.length }} teams</span>
            </div>

            <div
              class="pot-teams"
              :class="{
                'admin-mode': isAdmin,
                'drop-target': isDropTarget(potIndex),
              }"
              @dragover.prevent="onDragOver(potIndex)"
              @dragleave="onDragLeave"
              @drop="onDrop($event, potIndex)"
            >
              <div class="team-list">
                <div
                  v-for="team in pot.teams"
                  :key="team.id"
                  class="team-item team-compact"
                  :draggable="isAdmin"
                  @dragstart="onDragStart($event, team, potIndex)"
                  :class="{ dragging: draggedTeam?.id === team.id }"
                >
                  <span class="team-name-only">{{ team.name }}</span>
                  <div class="team-tooltip">
                    <div class="tooltip-content">
                      <div v-if="team.company" class="tooltip-item">
                        <strong>Company:</strong> {{ team.company }}
                      </div>
                      <div v-if="team.preferredDay" class="tooltip-item">
                        <strong>Preferred Day:</strong> {{ team.preferredDay }}
                      </div>
                      <div
                        v-if="team.restrictedDays && team.restrictedDays.length"
                        class="tooltip-item"
                      >
                        <strong>Only Available:</strong>
                        {{ team.restrictedDays.join(", ") }}
                      </div>
                      <div
                        v-if="team.members && team.members.length"
                        class="tooltip-item"
                      >
                        <strong>Members:</strong>
                        <!-- {{ team.members.map((m) => m.name).join(", ") }} -->
                        {{
                          team.members
                            .map((m) =>
                              (
                                m.firstName.trim() +
                                " " +
                                (m.lastName || "")
                              ).trim()
                            )
                            .join(", ")
                        }}
                      </div>
                      <!-- <div v-if="team.points" class="tooltip-item">
                        <strong>Points:</strong> {{ team.points }}
                      </div> -->
                    </div>
                  </div>
                </div>
              </div>
              <div
                v-if="isAdmin && pot.teams.length === 0"
                class="empty-pot-message"
              >
                Drop teams here
              </div>
            </div>
          </div>
        </div>
      </div>

      <!-- Group Drawing Section (Admin Only) -->
      <div v-if="isAdmin" class="draw-section">
        <h3>Draw Qualification Groups</h3>
        <p>
          Once teams are seeded into pots, you can run the draw to automatically
          assign teams to qualification groups.
        </p>

        <div class="form-group">
          <label for="num-groups">Number of Qualification Groups:</label>
          <input
            type="number"
            id="num-groups"
            v-model.number="numGroups"
            min="1"
            max="12"
          />
        </div>

        <div class="form-group">
          <label for="teams-per-group">Teams per Group:</label>
          <input
            type="number"
            id="teams-per-group"
            v-model.number="teamsPerGroup"
            min="3"
            max="8"
          />
          <p class="note">
            Total Capacity: {{ numGroups * teamsPerGroup }} teams
            <br />
            Total Teams: {{ totalTeams }}
            <span v-if="numGroups * teamsPerGroup < totalTeams" class="warning">
              (Not enough capacity!)
            </span>
          </p>
        </div>

        <!-- Seeding Restrictions -->
        <div class="restriction-controls">
          <h4>Draw Restrictions</h4>
          <div class="form-group">
            <label class="checkbox-label">
              <input type="checkbox" v-model="restrictions.avoidSameCompany" />
              Avoid teams from same company in the same group
            </label>
          </div>
          <div class="form-group">
            <label class="checkbox-label">
              <input type="checkbox" v-model="restrictions.balanceMatchDays" />
              Try to balance preferred match days within groups
            </label>
          </div>
          <div class="form-group">
            <label class="checkbox-label">
              <input
                type="checkbox"
                v-model="restrictions.respectRestrictedDays"
              />
              Respect teams' day restrictions (if a team can only play on
              certain days)
            </label>
          </div>
        </div>

        <button
          @click="runDraw"
          class="btn draw-btn"
          :disabled="numGroups * teamsPerGroup < totalTeams"
        >
          Run Qualification Group Draw
        </button>

        <div v-if="drawComplete" class="draw-results">
          <h4>
            Draw Results <span class="edit-hint">(Drag and drop to edit)</span>
          </h4>
          <div class="draw-groups">
            <div
              v-for="(group, groupIndex) in drawResults"
              :key="`group-${groupIndex}`"
              class="draw-group"
              @dragover.prevent="onGroupDragOver(groupIndex)"
              @dragleave="onGroupDragLeave"
              @drop="onGroupDrop($event, groupIndex)"
              :class="{ 'drop-target': groupDragTarget === groupIndex }"
            >
              <h5>Group {{ groupIndex + 1 }}</h5>
              <ul>
                <li
                  v-for="team in group"
                  :key="team.id"
                  :draggable="true"
                  @dragstart="onGroupDragStart($event, team, groupIndex)"
                  :class="{ dragging: groupDraggedTeam?.id === team.id }"
                  class="group-team-item"
                >
                  {{ team.name }}
                  <div class="team-tooltip group-tooltip">
                    <div class="tooltip-content">
                      <div class="tooltip-item">
                        <strong>Pot:</strong> {{ team.potId + 1 }}
                      </div>
                      <div v-if="team.company" class="tooltip-item">
                        <strong>Company:</strong> {{ team.company }}
                      </div>
                      <div v-if="team.preferredDay" class="tooltip-item">
                        <strong>Preferred Day:</strong> {{ team.preferredDay }}
                      </div>
                      <div
                        v-if="team.restrictedDays && team.restrictedDays.length"
                        class="tooltip-item"
                      >
                        <strong>Only Available:</strong>
                        {{ team.restrictedDays.join(", ") }}
                      </div>
                    </div>
                  </div>
                </li>
              </ul>
              <div
                v-if="getGroupConflicts(group).length > 0"
                class="group-conflicts"
              >
                <div class="conflict-title">Conflicts:</div>
                <ul class="conflict-list">
                  <li
                    v-for="(conflict, idx) in getGroupConflicts(group)"
                    :key="idx"
                    class="conflict-item"
                  >
                    {{ conflict }}
                  </li>
                </ul>
              </div>
            </div>
          </div>

          <button @click="runDraw" class="btn redraw-btn">Re-Run Draw</button>

          <button @click="saveDrawResults" class="btn save-draw-btn">
            Save Draw Results
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted, reactive } from "vue";
import {
  collection,
  getDocs,
  doc,
  setDoc,
  getDoc,
  updateDoc,
  deleteDoc,
  query,
  orderBy,
  where,
} from "firebase/firestore";
import { db } from "@/firebase";

export default {
  name: "TeamSeeding",

  props: {
    isAdmin: {
      type: Boolean,
      default: false,
    },
  },

  setup(props) {
    const teams = ref([]);
    const unassignedTeams = ref([]);
    const pots = ref([]);
    const numPots = ref(4);
    const loading = ref(true);
    const error = ref(null);

    // Drag and drop state for pots
    const draggedTeam = ref(null);
    const dragSourcePot = ref(null);
    const dragTargetPot = ref(null);

    // Drag and drop state for draw results
    const groupDraggedTeam = ref(null);
    const groupDragSourceIndex = ref(null);
    const groupDragTarget = ref(null);

    // Group draw variables
    const numGroups = ref(8);
    const teamsPerGroup = ref(5);
    const drawResults = ref([]);
    const drawComplete = ref(false);

    // Restrictions
    const restrictions = reactive({
      avoidSameCompany: true,
      balanceMatchDays: true,
      respectRestrictedDays: true,
    });

    // Computed properties
    const totalTeams = computed(() => {
      return teams.value.length;
    });

    // Returns teams that are not assigned to any pot
    const getUnassignedTeams = () => {
      return teams.value.filter(
        (team) =>
          team.potId === "unassigned" ||
          team.potId === null ||
          team.potId === undefined
      );
    };

    // Drag and drop methods for pots
    const onDragStart = (event, team, sourcePotIndex) => {
      draggedTeam.value = team;
      dragSourcePot.value = sourcePotIndex;

      // Add data to the drag event
      event.dataTransfer.effectAllowed = "move";
      event.dataTransfer.setData("text/plain", team.id);

      // Add a class to the dragged element
      setTimeout(() => {
        event.target.classList.add("dragging");
      }, 0);
    };

    const onDragOver = (potIndex) => {
      // Allow drops on any pot (including unassigned)
      dragTargetPot.value = potIndex;
    };

    const onDragLeave = () => {
      dragTargetPot.value = null;
    };

    const onDrop = async (event, potIndex) => {
      event.preventDefault();

      // Only process drops if we have a valid team
      if (draggedTeam.value && dragSourcePot.value !== potIndex) {
        // Update the team's pot assignment
        const team = draggedTeam.value;
        const oldPotId = team.potId;

        // Set new pot ID
        team.potId = potIndex;

        // Move the team in the UI
        if (oldPotId === "unassigned") {
          // Remove from unassigned list
          const teamIndex = unassignedTeams.value.findIndex(
            (t) => t.id === team.id
          );
          if (teamIndex !== -1) {
            unassignedTeams.value.splice(teamIndex, 1);
          }

          // Add to target pot
          if (potIndex !== "unassigned") {
            pots.value[potIndex].teams.push(team);
          }
        } else if (potIndex === "unassigned") {
          // Remove from source pot
          const sourcePot = pots.value[oldPotId];
          const teamIndex = sourcePot.teams.findIndex((t) => t.id === team.id);
          if (teamIndex !== -1) {
            sourcePot.teams.splice(teamIndex, 1);
          }

          // Add to unassigned
          unassignedTeams.value.push(team);
        } else {
          // Moving between pots
          // Remove from source pot
          const sourcePot = pots.value[oldPotId];
          const teamIndex = sourcePot.teams.findIndex((t) => t.id === team.id);
          if (teamIndex !== -1) {
            sourcePot.teams.splice(teamIndex, 1);
          }

          // Add to target pot
          pots.value[potIndex].teams.push(team);
        }

        // Save the change to Firestore
        try {
          await updateDoc(doc(db, "teams", team.id), {
            potId: potIndex === "unassigned" ? null : potIndex,
          });
        } catch (err) {
          console.error("Error updating team pot:", err);
        }
      }

      // Reset drag state
      draggedTeam.value = null;
      dragSourcePot.value = null;
      dragTargetPot.value = null;
    };

    const isDropTarget = (potIndex) => {
      return dragTargetPot.value === potIndex;
    };

    // Drag and drop methods for draw results
    const onGroupDragStart = (event, team, groupIndex) => {
      groupDraggedTeam.value = team;
      groupDragSourceIndex.value = groupIndex;

      // Add data to the drag event
      event.dataTransfer.effectAllowed = "move";
      event.dataTransfer.setData("text/plain", team.id);

      // Add a class to the dragged element
      setTimeout(() => {
        event.target.classList.add("dragging");
      }, 0);
    };

    const onGroupDragOver = (groupIndex) => {
      groupDragTarget.value = groupIndex;
    };

    const onGroupDragLeave = () => {
      groupDragTarget.value = null;
    };

    const onGroupDrop = (event, groupIndex) => {
      event.preventDefault();

      // Only process drops if we have a valid team and we're dropping to a different group
      if (groupDraggedTeam.value && groupDragSourceIndex.value !== groupIndex) {
        const team = groupDraggedTeam.value;
        const sourceGroupIndex = groupDragSourceIndex.value;

        // Remove from source group
        const sourceGroup = drawResults.value[sourceGroupIndex];
        const teamIndex = sourceGroup.findIndex((t) => t.id === team.id);
        if (teamIndex !== -1) {
          sourceGroup.splice(teamIndex, 1);
        }

        // Add to target group
        drawResults.value[groupIndex].push(team);
      }

      // Reset drag state
      groupDraggedTeam.value = null;
      groupDragSourceIndex.value = null;
      groupDragTarget.value = null;
    };

    // Check for conflicts within a group
    const getGroupConflicts = (group) => {
      const conflicts = [];

      // Check for company conflicts
      const companies = {};
      group.forEach((team) => {
        if (team.company) {
          if (!companies[team.company]) {
            companies[team.company] = [];
          }
          companies[team.company].push(team.name);
        }
      });

      for (const company in companies) {
        if (companies[company].length > 1) {
          conflicts.push(
            `Company conflict: ${companies[company].join(", ")} (${company})`
          );
        }
      }

      // Check for day conflicts
      const teamsWithRestrictedDays = group.filter(
        (team) => team.restrictedDays && team.restrictedDays.length > 0
      );

      if (teamsWithRestrictedDays.length > 1) {
        // Check for incompatible day restrictions
        for (let i = 0; i < teamsWithRestrictedDays.length; i++) {
          for (let j = i + 1; j < teamsWithRestrictedDays.length; j++) {
            const teamA = teamsWithRestrictedDays[i];
            const teamB = teamsWithRestrictedDays[j];

            // If teams have mutually exclusive day restrictions
            const hasCommonDay = teamA.restrictedDays.some((day) =>
              teamB.restrictedDays.includes(day)
            );

            if (!hasCommonDay) {
              conflicts.push(
                `Day conflict: ${teamA.name} and ${teamB.name} have incompatible day restrictions`
              );
            }
          }
        }
      }

      return conflicts;
    };

    // Initialize pots
    const updatePots = () => {
      // Preserve existing pot assignments if possible
      const oldPots = [...pots.value];

      // Create new pots array with specified number of pots
      pots.value = [];
      for (let i = 0; i < numPots.value; i++) {
        // Try to keep teams that were already in this pot
        const existingTeams = oldPots[i] ? [...oldPots[i].teams] : [];
        pots.value.push({
          id: i,
          name: `Pot ${i + 1}`,
          teams: existingTeams,
        });
      }

      // Update unassigned teams
      unassignedTeams.value = getUnassignedTeams();
    };

    // Load data
    const loadData = async () => {
      try {
        loading.value = true;

        // Load all teams
        const teamsQuery = query(collection(db, "teams"), orderBy("name"));
        const teamsSnapshot = await getDocs(teamsQuery);
        teams.value = [];

        teamsSnapshot.forEach((doc) => {
          // Force all teams to be unassigned initially for seeding UI
          const teamData = doc.data();
          teams.value.push({
            id: doc.id,
            ...teamData,
            potId: "unassigned", // Always start with unassigned for this UI
            originalPotId: teamData.potId, // Store the original potId for reference
          });
        });

        // Load seeding configuration
        const seedingDoc = await getDoc(doc(db, "settings", "seeding"));
        if (seedingDoc.exists()) {
          const seedingData = seedingDoc.data();
          numPots.value = seedingData.numPots || 4;

          if (seedingData.restrictions) {
            restrictions.avoidSameCompany =
              seedingData.restrictions.avoidSameCompany || true;
            restrictions.balanceMatchDays =
              seedingData.restrictions.balanceMatchDays || true;
            restrictions.respectRestrictedDays =
              seedingData.restrictions.respectRestrictedDays || true;
          }
        } else if (props.isAdmin) {
          // Create default settings if they don't exist
          await setDoc(doc(db, "settings", "seeding"), {
            numPots: numPots.value,
            restrictions: restrictions,
            createdAt: new Date(),
          });
        }

        // Initialize pots (empty) and all teams should be unassigned
        updatePots();
        unassignedTeams.value = [...teams.value]; // Set all teams as unassigned

        loading.value = false;
      } catch (err) {
        error.value = "Failed to load data. Please try again.";
        loading.value = false;
        console.error(err);
      }
    };

    // Auto-assign teams to pots based on previous performance
    const autoAssignTeams = () => {
      // Clear existing pot assignments
      pots.value.forEach((pot) => {
        pot.teams = [];
      });

      // Get all teams (including unassigned)
      const allTeams = [...teams.value];

      // Sort teams by rank (using points as a proxy for rank)
      const sortedTeams = allTeams.sort((a, b) => {
        return (b.points || 0) - (a.points || 0);
      });

      // Distribute teams evenly among pots
      const teamsPerPot = Math.ceil(sortedTeams.length / numPots.value);

      sortedTeams.forEach((team, index) => {
        const potIndex = Math.min(
          Math.floor(index / teamsPerPot),
          numPots.value - 1
        );
        team.potId = potIndex;
        pots.value[potIndex].teams.push(team);
      });

      // Clear unassigned teams
      unassignedTeams.value = [];

      // Save the pot assignments to Firestore
      saveTeamPots();
    };

    // Reset all team assignments
    const resetTeamAssignment = () => {
      // Collect all teams from both pots and the current unassigned list
      const allTeams = [];

      // Collect teams from pots
      pots.value.forEach((pot) => {
        allTeams.push(...pot.teams);
        pot.teams = []; // Clear the pot
      });

      // Add any existing unassigned teams
      allTeams.push(...unassignedTeams.value);

      // Move all teams to unassigned and update their potId
      allTeams.forEach((team) => {
        team.potId = "unassigned";
      });

      // Update the unassigned teams list
      unassignedTeams.value = allTeams;

      // Save the changes to Firestore
      saveTeamPots();
    };

    // Update a team's pot assignment
    const updateTeamPot = async (team) => {
      const oldPotId =
        dragSourcePot.value !== null
          ? dragSourcePot.value
          : team.potId === "unassigned"
          ? "unassigned"
          : parseInt(team.potId);

      const newPotId = team.potId;

      // Prevent duplicate assignments by checking if team already exists in the target pot
      if (newPotId !== "unassigned") {
        const targetPot = pots.value[newPotId];
        const existingTeam = targetPot.teams.find((t) => t.id === team.id);
        if (existingTeam) {
          console.warn(
            "Team already exists in this pot, skipping reassignment"
          );
          return;
        }
      }

      // If moving to/from unassigned
      if (oldPotId === "unassigned" && newPotId !== "unassigned") {
        // Remove from unassigned
        const teamIndex = unassignedTeams.value.findIndex(
          (t) => t.id === team.id
        );
        if (teamIndex !== -1) {
          unassignedTeams.value.splice(teamIndex, 1);
        }

        // Add to pot
        pots.value[newPotId].teams.push(team);
      } else if (oldPotId !== "unassigned" && newPotId === "unassigned") {
        // Remove from pot
        const teamIndex = pots.value[oldPotId].teams.findIndex(
          (t) => t.id === team.id
        );
        if (teamIndex !== -1) {
          pots.value[oldPotId].teams.splice(teamIndex, 1);
        }

        // Add to unassigned
        unassignedTeams.value.push(team);
      } else if (
        oldPotId !== "unassigned" &&
        newPotId !== "unassigned" &&
        oldPotId !== newPotId
      ) {
        // Remove from old pot
        const teamIndex = pots.value[oldPotId].teams.findIndex(
          (t) => t.id === team.id
        );
        if (teamIndex !== -1) {
          pots.value[oldPotId].teams.splice(teamIndex, 1);
        }

        // Add to new pot
        pots.value[newPotId].teams.push(team);
      }

      // Save to Firestore
      try {
        await updateDoc(doc(db, "teams", team.id), {
          potId: newPotId === "unassigned" ? null : newPotId,
        });
      } catch (err) {
        console.error("Error updating team pot:", err);
      }
    };

    // Save all team pot assignments
    const saveTeamPots = async () => {
      try {
        const promises = [];

        // Save pot assignments for teams in pots
        pots.value.forEach((pot) => {
          pot.teams.forEach((team) => {
            promises.push(
              updateDoc(doc(db, "teams", team.id), {
                potId: team.potId,
              })
            );
          });
        });

        // Save unassigned teams
        unassignedTeams.value.forEach((team) => {
          promises.push(
            updateDoc(doc(db, "teams", team.id), {
              potId: null,
            })
          );
        });

        await Promise.all(promises);
        // alert("Team pot assignments saved successfully!");
      } catch (err) {
        console.error("Error saving team pot assignments:", err);
        alert("Error saving team pot assignments");
      }
    };

    // Save seeding restrictions
    const saveRestrictions = async () => {
      try {
        await setDoc(
          doc(db, "settings", "seeding"),
          {
            numPots: numPots.value,
            restrictions: restrictions,
            updatedAt: new Date(),
          },
          { merge: true }
        );

        alert("Seeding restrictions saved successfully!");
      } catch (err) {
        console.error("Error saving seeding restrictions:", err);
        alert("Error saving seeding restrictions");
      }
    };

    // Check if two teams have conflicting day restrictions
    const hasDateConflict = (teamA, teamB) => {
      // If either team has no restricted days, they can play anytime
      if (
        !teamA.restrictedDays ||
        !teamA.restrictedDays.length ||
        !teamB.restrictedDays ||
        !teamB.restrictedDays.length
      ) {
        return false;
      }

      // Check if teams have at least one common day they both can play
      const hasCommonDay = teamA.restrictedDays.some((day) =>
        teamB.restrictedDays.includes(day)
      );

      return !hasCommonDay;
    };

    // Check if two teams have the same preferred day
    const hasSamePreferredDay = (teamA, teamB) => {
      return (
        teamA.preferredDay &&
        teamB.preferredDay &&
        teamA.preferredDay === teamB.preferredDay
      );
    };

    // Run the qualification group draw
    const runDraw = () => {
      // Clear previous draw results
      drawResults.value = [];

      // Initialize empty groups
      for (let i = 0; i < numGroups.value; i++) {
        drawResults.value.push([]);
      }

      // Collect all teams from pots
      const potTeams = {};
      for (let potIndex = 0; potIndex < pots.value.length; potIndex++) {
        potTeams[potIndex] = [...pots.value[potIndex].teams];
      }

      // Draw teams from each pot into groups
      // Start with pot 1 (strongest teams)
      for (let potIndex = 0; potIndex < pots.value.length; potIndex++) {
        const teamsInPot = potTeams[potIndex];

        // Skip empty pots
        if (!teamsInPot || teamsInPot.length === 0) continue;

        // Shuffle teams in this pot
        const shuffledTeams = shuffle([...teamsInPot]);

        // Process each team in this pot
        for (const team of shuffledTeams) {
          // Start with a random group for even distribution
          const startGroupIndex = Math.floor(Math.random() * numGroups.value);
          let bestGroupIndex = -1;
          let bestConflictScore = Infinity;

          // Try to find the best group for this team
          for (
            let groupOffset = 0;
            groupOffset < numGroups.value;
            groupOffset++
          ) {
            const groupIndex =
              (startGroupIndex + groupOffset) % numGroups.value;
            const group = drawResults.value[groupIndex];

            // Skip groups that already have a team from this pot or are full
            if (
              group.some((t) => t.potId === potIndex) ||
              group.length >= teamsPerGroup.value
            ) {
              continue;
            }

            // Calculate conflict score for this group
            let conflictScore = 0;

            // Check for company conflicts
            if (restrictions.avoidSameCompany) {
              for (const existingTeam of group) {
                if (
                  existingTeam.company &&
                  team.company &&
                  existingTeam.company === team.company
                ) {
                  conflictScore += 100; // High penalty for company conflicts
                }
              }
            }

            // Check for day restriction conflicts
            if (restrictions.respectRestrictedDays) {
              for (const existingTeam of group) {
                if (hasDateConflict(existingTeam, team)) {
                  conflictScore += 200; // Very high penalty for day restriction conflicts
                }
              }
            }

            // Balance preferred days if needed
            if (restrictions.balanceMatchDays && team.preferredDay) {
              for (const existingTeam of group) {
                if (hasSamePreferredDay(existingTeam, team)) {
                  conflictScore += 10; // Smaller penalty for preferred day conflicts
                }
              }
            }

            // If this group has a better score, select it
            if (conflictScore < bestConflictScore) {
              bestConflictScore = conflictScore;
              bestGroupIndex = groupIndex;

              // If no conflicts, we can stop searching
              if (conflictScore === 0) break;
            }
          }

          // If we found a suitable group, add the team
          if (bestGroupIndex !== -1) {
            drawResults.value[bestGroupIndex].push(team);
          } else {
            // Find the group with fewest teams as fallback
            let minTeamsGroup = 0;
            for (let g = 1; g < drawResults.value.length; g++) {
              if (
                drawResults.value[g].length <
                drawResults.value[minTeamsGroup].length
              ) {
                minTeamsGroup = g;
              }
            }

            // Add to group with fewest teams
            if (drawResults.value[minTeamsGroup].length < teamsPerGroup.value) {
              drawResults.value[minTeamsGroup].push(team);
            }
          }
        }
      }

      drawComplete.value = true;
    };

    // Save draw results to Firestore
    const saveDrawResults = async () => {
      try {
        // First, check if there are existing qualification groups
        const groupsQuery = query(
          collection(db, "groups"),
          where("type", "==", "qualification")
        );
        const existingGroups = await getDocs(groupsQuery);

        // If there are existing groups, show confirmation dialog
        if (!existingGroups.empty) {
          const confirmDelete = confirm(
            `Warning: ${existingGroups.size} existing qualification groups found. Creating new groups will delete all existing qualification groups and reset team assignments. Are you sure you want to continue?`
          );

          if (!confirmDelete) {
            return; // User canceled the operation
          }

          // Delete all existing qualification groups
          const deletePromises = [];
          existingGroups.forEach((groupDoc) => {
            deletePromises.push(deleteDoc(doc(db, "groups", groupDoc.id)));
          });

          // Wait for all deletes to complete
          await Promise.all(deletePromises);
        }

        // Now create the new qualification groups
        const promises = [];

        // Create the groups
        for (let i = 0; i < drawResults.value.length; i++) {
          const groupData = {
            name: `Qualification Group ${i + 1}`,
            type: "qualification",
            order: i,
            isActive: true,
            createdAt: new Date(),
          };

          // Create the group
          const groupRef = doc(collection(db, "groups"));
          promises.push(setDoc(groupRef, groupData));

          // Update each team with their group assignment
          const teams = drawResults.value[i];
          teams.forEach((team) => {
            const teamRef = doc(db, "teams", team.id);
            promises.push(
              updateDoc(teamRef, {
                groupId: groupRef.id,
                // Keep their potId for reference
                potId: team.potId,
              })
            );
          });
        }

        await Promise.all(promises);

        // Update tournament phase
        await setDoc(
          doc(db, "settings", "tournament"),
          {
            phase: "qualification",
            updatedAt: new Date(),
          },
          { merge: true }
        );

        alert(
          "Draw results saved successfully! Tournament phase updated to Qualification."
        );
      } catch (err) {
        console.error("Error saving draw results:", err);
        alert("Error saving draw results: " + err.message);
      }
    };

    // Helper function to shuffle array
    const shuffle = (array) => {
      let currentIndex = array.length,
        randomIndex;

      // While there remain elements to shuffle
      while (currentIndex !== 0) {
        // Pick a remaining element
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex--;

        // Swap it with the current element
        [array[currentIndex], array[randomIndex]] = [
          array[randomIndex],
          array[currentIndex],
        ];
      }

      return array;
    };

    // Initialize component
    onMounted(() => {
      loadData();
    });

    return {
      teams,
      pots,
      unassignedTeams,
      numPots,
      loading,
      error,
      restrictions,
      numGroups,
      teamsPerGroup,
      totalTeams,
      drawResults,
      drawComplete,
      draggedTeam,
      dragSourcePot,
      dragTargetPot,
      groupDraggedTeam,
      groupDragSourceIndex,
      groupDragTarget,
      updatePots,
      autoAssignTeams,
      resetTeamAssignment,
      updateTeamPot,
      saveTeamPots,
      saveRestrictions,
      runDraw,
      saveDrawResults,
      onDragStart,
      onDragOver,
      onDragLeave,
      onDrop,
      isDropTarget,
      onGroupDragStart,
      onGroupDragOver,
      onGroupDragLeave,
      onGroupDrop,
      getGroupConflicts,
    };
  },
};
</script>

