<template>
  <div class="matches-container">
    <h2>
      {{ phase === "qualification" ? "Qualification" : "Division" }} Matches
    </h2>

    <div v-if="loading" class="loading-container">
      <div class="loading-spinner"></div>
      <p>Loading matches...</p>
    </div>

    <div v-else-if="error" class="error-container">
      <i class="error-icon">!</i>
      <p>{{ error }}</p>
    </div>

    <div v-else>
      <!-- Always visible action buttons for admins -->
      <div class="action-bar" v-if="isAdmin">
        <button
          @click="showScheduleModal = true"
          class="action-btn schedule-btn"
        >
          <i class="icon-plus">+</i> Add Match
        </button>
        <button
          @click="showGenerateModal = true"
          class="action-btn generate-btn"
        >
          <i class="icon-calendar">📅</i> Generate Matches
        </button>
        <button @click="confirmDeleteAll" class="action-btn delete-all-btn">
          <i class="icon-trash">🗑️</i> Delete All Matches
        </button>
      </div>

      <!-- Group filter dropdown -->
      <div class="filter-controls">
        <div class="form-group">
          <label for="group-filter"
            >Filter by
            {{ phase === "qualification" ? "Group" : "Division" }}:</label
          >
          <select id="group-filter" v-model="groupFilter" class="filter-select">
            <option value="">
              All {{ phase === "qualification" ? "Groups" : "Divisions" }}
            </option>
            <option v-for="group in groups" :key="group.id" :value="group.id">
              {{ group.name }}
            </option>
          </select>
        </div>
      </div>

      <div v-if="groupedMatches.length > 0" class="match-list">
        <div
          v-for="(group, index) in groupedMatches"
          :key="index"
          class="match-group"
        >
          <div class="date-header">
            <h3>{{ formatDateHeader(group.date) }}</h3>
            <span class="match-count"
              >{{ group.matches.length }}
              {{ group.matches.length === 1 ? "match" : "matches" }}</span
            >
          </div>

          <div class="match-table">
            <table>
              <thead>
                <tr>
                  <th class="team1-column">Home Team</th>
                  <th class="vs-column"></th>
                  <th class="team2-column">Away Team</th>
                  <th class="time-column">Time</th>
                  <th class="location-column">Tables</th>
                  <th v-if="isAdmin" class="actions-header">Actions</th>
                </tr>
              </thead>
              <tbody>
                <tr
                  v-for="match in group.matches"
                  :key="match.id"
                  class="match-row"
                >
                  <td class="match-team home-team">
                    {{ match.homeTeam || match.player1 }}
                  </td>
                  <td class="vs-column">🆚</td>
                  <td class="match-team away-team">
                    {{ match.awayTeam || match.player2 }}
                  </td>
                  <td class="match-time time-column">
                    {{ match.time || "TBD" }}
                  </td>
                  <td class="location-column">
                    {{ match.location || "-" }}
                  </td>
                  <td v-if="isAdmin" class="row-actions">
                    <div class="action-buttons">
                      <button
                        @click="editMatch(match)"
                        class="row-btn edit-btn"
                        title="Edit Match"
                      >
                        Edit
                      </button>
                      <button
                        @click="assignResult(match)"
                        class="row-btn score-btn"
                        title="Add Result"
                      >
                        Result
                      </button>
                      <button
                        @click="confirmDelete(match)"
                        class="row-btn delete-btn"
                        title="Delete Match"
                      >
                        Delete
                      </button>
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>

      <div v-else class="no-matches">
        <p>
          No upcoming
          {{ phase === "qualification" ? "qualification" : "division" }}
          matches found
        </p>
      </div>
    </div>

    <!-- Schedule New Match Modal -->
    <div v-if="showScheduleModal" class="modal-overlay">
      <div class="modal-content">
        <div class="modal-header">
          <h3>
            Schedule New
            {{ phase === "qualification" ? "Qualification" : "Division" }}
            Match
          </h3>
          <button @click="showScheduleModal = false" class="close-btn">
            ×
          </button>
        </div>
        <div class="modal-body">
          <div class="form-group">
            <label for="new-match-group"
              >{{ phase === "qualification" ? "Group" : "Division" }}:</label
            >
            <select id="new-match-group" v-model="newMatch.groupId" required>
              <option value="">
                -- Select
                {{ phase === "qualification" ? "group" : "division" }} --
              </option>
              <option v-for="group in groups" :key="group.id" :value="group.id">
                {{ group.name }}
              </option>
            </select>
          </div>

          <div class="form-row">
            <div class="form-group">
              <label for="new-match-date">Date:</label>
              <input
                id="new-match-date"
                type="date"
                v-model="newMatch.dateString"
                required
              />
            </div>
            <div class="form-group">
              <label for="new-match-time">Time:</label>
              <input id="new-match-time" type="time" v-model="newMatch.time" />
            </div>
          </div>

          <div class="form-row">
            <div class="form-group">
              <label for="new-match-home-team">Home Team:</label>
              <select
                id="new-match-home-team"
                v-model="newMatch.homeTeam"
                required
              >
                <option value="">-- Select team --</option>
                <option
                  v-for="team in filteredTeams"
                  :key="`home-${team.id}`"
                  :value="team.name"
                >
                  {{ team.name }}
                </option>
              </select>
            </div>
            <div class="form-group">
              <label for="new-match-away-team">Away Team:</label>
              <select
                id="new-match-away-team"
                v-model="newMatch.awayTeam"
                required
              >
                <option value="">-- Select team --</option>
                <option
                  v-for="team in filteredTeams"
                  :key="`away-${team.id}`"
                  :value="team.name"
                >
                  {{ team.name }}
                </option>
              </select>
            </div>
          </div>

          <div class="form-group">
            <label for="new-match-location">Tables:</label>
            <input
              type="text"
              id="new-match-location"
              v-model="newMatch.location"
              placeholder="Match location"
            />
          </div>
        </div>
        <div class="modal-footer">
          <button @click="scheduleMatch" class="btn save-btn">
            Schedule Match
          </button>
          <button @click="showScheduleModal = false" class="btn cancel-btn">
            Cancel
          </button>
        </div>
      </div>
    </div>

    <!-- Edit Match Modal -->
    <div v-if="showEditModal" class="modal-overlay">
      <div class="modal-content">
        <div class="modal-header">
          <h3>
            Edit
            {{ phase === "qualification" ? "Qualification" : "Division" }}
            Match
          </h3>
          <button @click="showEditModal = false" class="close-btn">×</button>
        </div>
        <div class="modal-body">
          <div class="form-group">
            <label for="edit-match-group"
              >{{ phase === "qualification" ? "Group" : "Division" }}:</label
            >
            <select id="edit-match-group" v-model="editingMatch.groupId">
              <option value="">
                -- Select
                {{ phase === "qualification" ? "group" : "division" }} --
              </option>
              <option v-for="group in groups" :key="group.id" :value="group.id">
                {{ group.name }}
              </option>
            </select>
          </div>

          <div class="form-row">
            <div class="form-group">
              <label :for="`match-date-${editingMatch.id}`">Date:</label>
              <input
                :id="`match-date-${editingMatch.id}`"
                type="date"
                v-model="editingMatch.dateString"
              />
            </div>
            <div class="form-group">
              <label :for="`match-time-${editingMatch.id}`">Time:</label>
              <input
                :id="`match-time-${editingMatch.id}`"
                type="time"
                v-model="editingMatch.time"
              />
            </div>
          </div>

          <div class="form-row">
            <div class="form-group">
              <label :for="`home-team-${editingMatch.id}`">Home Team:</label>
              <select
                :id="`home-team-${editingMatch.id}`"
                v-model="editingMatch.homeTeam"
              >
                <option value="">-- Select team --</option>
                <option
                  v-for="team in teamsForEditGroup"
                  :key="`home-${team.id}`"
                  :value="team.name"
                >
                  {{ team.name }}
                </option>
              </select>
            </div>
            <div class="form-group">
              <label :for="`away-team-${editingMatch.id}`">Away Team:</label>
              <select
                :id="`away-team-${editingMatch.id}`"
                v-model="editingMatch.awayTeam"
              >
                <option value="">-- Select team --</option>
                <option
                  v-for="team in teamsForEditGroup"
                  :key="`away-${team.id}`"
                  :value="team.name"
                >
                  {{ team.name }}
                </option>
              </select>
            </div>
          </div>

          <div class="form-group">
            <label :for="`location-${editingMatch.id}`">Tables:</label>
            <input
              :id="`location-${editingMatch.id}`"
              type="text"
              v-model="editingMatch.location"
              placeholder="Match location"
            />
          </div>
        </div>
        <div class="modal-footer">
          <button @click="updateMatch" class="btn save-btn">
            Save Changes
          </button>
          <button @click="showEditModal = false" class="btn cancel-btn">
            Cancel
          </button>
        </div>
      </div>
    </div>

    <!-- Match Result Form -->
    <MatchResultForm
      :showResultModal="showResultModal"
      :matchData="editingMatch"
      :teams="teams"
      @save-result="saveMatchResult"
      @close="showResultModal = false"
    />

    <!-- Delete Confirmation Modal -->
    <div v-if="showDeleteModal" class="modal-overlay">
      <div class="modal-content">
        <div class="modal-header">
          <h3>Confirm Delete</h3>
          <button @click="showDeleteModal = false" class="close-btn">×</button>
        </div>
        <div class="modal-body">
          <p>Are you sure you want to delete this upcoming match?</p>
          <div class="match-info">
            <strong
              >{{ matchToDelete.homeTeam }} 🆚
              {{ matchToDelete.awayTeam }}</strong
            >
            <br />
            <span>{{ formatDate(matchToDelete.date) }}</span>
          </div>
          <p class="warning">This action cannot be undone.</p>
        </div>
        <div class="modal-footer">
          <button @click="deleteMatch" class="btn delete-btn">Delete</button>
          <button @click="showDeleteModal = false" class="btn cancel-btn">
            Cancel
          </button>
        </div>
      </div>
    </div>

    <!-- Match Generation Modal -->
    <div v-if="showGenerateModal" class="modal-overlay">
      <div class="modal-content large-modal">
        <div class="modal-header">
          <h3>
            Generate
            {{ phase === "qualification" ? "Qualification" : "Division" }}
            Matches
          </h3>
          <button @click="showGenerateModal = false" class="close-btn">
            ×
          </button>
        </div>
        <div class="modal-body">
          <div class="form-group">
            <label for="generate-group"
              >{{ phase === "qualification" ? "Group" : "Division" }}:</label
            >
            <select
              id="generate-group"
              v-model="generateSettings.groupId"
              required
            >
              <option value="">
                -- Select
                {{ phase === "qualification" ? "group" : "division" }} --
              </option>
              <option v-for="group in groups" :key="group.id" :value="group.id">
                {{ group.name }}
              </option>
              <option value="all">
                All {{ phase === "qualification" ? "Groups" : "Divisions" }}
              </option>
            </select>
          </div>

          <div class="form-row">
            <div class="form-group">
              <label for="generate-start-date">Start Date:</label>
              <input
                id="generate-start-date"
                type="date"
                v-model="generateSettings.startDate"
                @change="generatePotentialMatchDays"
                required
              />
            </div>
            <div class="form-group">
              <label for="generate-end-date">End Date:</label>
              <input
                id="generate-end-date"
                type="date"
                v-model="generateSettings.endDate"
                @change="generatePotentialMatchDays"
              />
            </div>
          </div>

          <div class="form-group">
            <label>Match Days:</label>
            <div class="checkbox-group">
              <label class="checkbox-label">
                <input
                  type="checkbox"
                  v-model="generateSettings.matchDays.monday"
                  @change="generatePotentialMatchDays"
                />
                Monday
              </label>
              <label class="checkbox-label">
                <input
                  type="checkbox"
                  v-model="generateSettings.matchDays.wednesday"
                  @change="generatePotentialMatchDays"
                />
                Wednesday
              </label>
            </div>
          </div>

          <div class="form-group">
            <label>Match Times:</label>
            <div class="checkbox-group">
              <label class="checkbox-label">
                <input
                  type="checkbox"
                  v-model="generateSettings.matchTimes['20:00']"
                />
                20:00
              </label>
              <label class="checkbox-label">
                <input
                  type="checkbox"
                  v-model="generateSettings.matchTimes['21:00']"
                />
                21:00
              </label>
            </div>
          </div>

          <div class="form-group">
            <label for="generate-location">Default Location:</label>
            <input
              type="text"
              id="generate-location"
              v-model="generateSettings.location"
              placeholder="e.g. TBTK Hall"
            />
          </div>

          <!-- New section for selecting specific match dates -->
          <div class="form-group" v-if="potentialMatchDays.length > 0">
            <label>Select/Deselect Specific Match Dates:</label>
            <p class="help-text">
              Uncheck dates that should be excluded (holidays, facility
              closures, etc.)
            </p>
            <div class="match-days-grid">
              <div
                v-for="(day, index) in potentialMatchDays"
                :key="index"
                class="match-day-item"
              >
                <label class="checkbox-label match-day-checkbox">
                  <input type="checkbox" v-model="day.selected" />
                  <span :class="{ 'excluded-date': !day.selected }">
                    {{ formatDateHeader(day.date) }}
                  </span>
                </label>
              </div>
            </div>
          </div>

          <div class="form-group">
            <label class="checkbox-label">
              <input
                type="checkbox"
                v-model="generateSettings.respectPreferences"
              />
              Respect team day/time preferences
            </label>
          </div>

          <div class="form-group info-setting">
            <label class="checkbox-label">
              <input
                type="checkbox"
                v-model="generateSettings.limitOneMatchPerTeamPerWeek"
                checked
              />
              <span>Maximum 1 match per team per week</span>
              <span class="setting-tip">
                <i class="tip-icon">ⓘ</i>
                <span class="tooltip-text">
                  Recommended default: Ensures teams play only one match per
                  week
                </span>
              </span>
            </label>
          </div>

          <div class="info-message">
            <p>
              <strong>Match distribution strategy:</strong>
            </p>
            <p class="info-detail">
              • Teams play a maximum of 1 match per week<br />
              • Matches are balanced between Monday and Wednesday when
              possible<br />
              • Teams with day preferences (Monday Only/Wednesday Only) are
              respected<br />
              • Each table pair hosts one match per time slot
            </p>
          </div>

          <div class="info-message tables-info">
            <p>
              <strong>Tables organization:</strong> {{ maxTables }} tables ({{
                Math.floor(maxTables / 2)
              }}
              table pairs)
            </p>
            <p class="info-detail">
              • Each match uses one table pair (e.g., Tables 1-2)<br />
              • Each table pair can host one match at 20:00 and one match at
              21:00<br />
              • Maximum {{ Math.floor(maxTables / 2) }} matches per timeslot
            </p>
          </div>

          <div
            v-if="generateSettings.groupId === 'all'"
            class="warning-message"
          >
            <p>
              Note: Generating matches for all
              {{ phase === "qualification" ? "groups" : "divisions" }} may take
              a moment.
            </p>
          </div>
        </div>
        <div class="modal-footer">
          <button
            @click="generateMatches"
            class="btn generate-btn"
            :disabled="isGenerating || potentialMatchDays.length === 0"
          >
            {{ isGenerating ? "Generating..." : "Generate Matches" }}
          </button>
          <button @click="showGenerateModal = false" class="btn cancel-btn">
            Cancel
          </button>
        </div>
      </div>
    </div>

    <!-- Delete All Matches Confirmation Modal -->
    <div v-if="showDeleteAllModal" class="modal-overlay">
      <div class="modal-content">
        <div class="modal-header">
          <h3>Confirm Delete All Matches</h3>
          <button @click="showDeleteAllModal = false" class="close-btn">
            ×
          </button>
        </div>
        <div class="modal-body">
          <div class="alert-icon">⚠️</div>
          <p>
            Are you sure you want to delete <strong>ALL</strong>
            {{ phase === "qualification" ? "qualification" : "division" }}
            matches?
          </p>
          <p class="warning">
            This action cannot be undone and will remove all matches for the
            current phase.
          </p>
        </div>
        <div class="modal-footer">
          <button @click="deleteAllMatches" class="btn delete-btn">
            Delete All Matches
          </button>
          <button @click="showDeleteAllModal = false" class="btn cancel-btn">
            Cancel
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted, reactive, watch } from "vue";
import {
  collection,
  getDocs,
  doc,
  deleteDoc,
  updateDoc,
  addDoc,
  query,
  where,
  Timestamp,
  serverTimestamp,
  writeBatch,
  getDoc,
} from "firebase/firestore";
import { db } from "@/firebase";
import MatchResultForm from "@/components/MatchResultForm.vue";

export default {
  name: "UpcomingMatches",

  components: {
    MatchResultForm,
  },

  props: {
    isAdmin: {
      type: Boolean,
      default: false,
    },
    phase: {
      type: String,
      default: "division",
      validator: (value) => ["qualification", "division"].includes(value),
    },
  },

  setup(props) {
    // Helper function to get week number of a date
    const getWeekNumber = (date) => {
      const firstDayOfYear = new Date(date.getFullYear(), 0, 1);
      const pastDaysOfYear = (date - firstDayOfYear) / 86400000;
      return Math.ceil((pastDaysOfYear + firstDayOfYear.getDay() + 1) / 7);
    };

    const matches = ref([]);
    const teams = ref([]);
    const groups = ref([]);
    const loading = ref(true);
    const error = ref(null);
    const lastUpdated = ref(null);
    const groupFilter = ref("");

    // Modals
    const showEditModal = ref(false);
    const showDeleteModal = ref(false);
    const showResultModal = ref(false);
    const showScheduleModal = ref(false);
    const showGenerateModal = ref(false);
    const isGenerating = ref(false);
    const maxTables = ref(16); // Default value
    const showDeleteAllModal = ref(false);

    // Add this new ref for potential match days
    const potentialMatchDays = ref([]);

    // New match form
    const newMatch = reactive({
      homeTeam: "",
      awayTeam: "",
      dateString: "",
      time: "",
      groupId: "",
      phase: props.phase,
      location: "",
    });

    // Editing match
    const editingMatch = reactive({
      id: "",
      homeTeam: "",
      awayTeam: "",
      date: null,
      dateString: "",
      time: "",
      homeTeamScore: 0,
      awayTeamScore: 0,
      groupId: "",
      location: "",
      teamMatches: [],
    });

    // Match to delete
    const matchToDelete = reactive({
      id: "",
      homeTeam: "",
      awayTeam: "",
      date: null,
    });

    // Match generation settings
    const generateSettings = reactive({
      groupId: "",
      startDate: new Date().toISOString().split("T")[0],
      endDate: new Date(Date.now() + 30 * 24 * 60 * 60 * 1000)
        .toISOString()
        .split("T")[0], // Default to 30 days after start
      matchDays: {
        monday: true,
        wednesday: true,
      },
      matchTimes: {
        "20:00": true,
        "21:00": true,
      },
      location: "TBTK Hall",
      respectPreferences: true,
      limitOneMatchPerTeamPerWeek: true, // Updated to per week instead of per day
      homeAwayStrategy: "alternate", // Options: "alternate", "random", "preference-based"
    });

    // Format date to user-friendly string
    const formatDate = (dateString) => {
      const date = new Date(dateString);
      return date.toLocaleDateString("en-US", {
        weekday: "short",
        month: "short",
        day: "numeric",
      });
    };

    // Format date for header - fixed to always show the actual date
    const formatDateHeader = (dateString) => {
      const date = new Date(dateString);
      return date.toLocaleDateString("en-US", {
        weekday: "long",
        month: "long",
        day: "numeric",
        year: "numeric",
      });
    };

    // Filter for upcoming matches based on phase and groupFilter
    const filteredMatches = computed(() => {
      const today = new Date();
      today.setHours(0, 0, 0, 0);

      // Start with filtering by phase and completed status
      let result = matches.value.filter(
        (match) =>
          match.phase === props.phase &&
          !match.isCompleted &&
          new Date(match.date) >= today
      );

      // Then apply group filter if set
      if (groupFilter.value) {
        result = result.filter((match) => match.groupId === groupFilter.value);
      }

      return result;
    });

    // Teams filtered by group for the new match form
    const filteredTeams = computed(() => {
      if (!newMatch.groupId) return [];

      // Return teams that belong to the selected group
      if (props.phase === "qualification") {
        return teams.value.filter((team) => team.groupId === newMatch.groupId);
      } else {
        return teams.value.filter(
          (team) => team.divisionId === newMatch.groupId
        );
      }
    });

    // Teams for the edit form
    const teamsForEditGroup = computed(() => {
      if (!editingMatch.groupId) return [];

      // Return teams that belong to the editing match's group
      if (props.phase === "qualification") {
        return teams.value.filter(
          (team) => team.groupId === editingMatch.groupId
        );
      } else {
        return teams.value.filter(
          (team) => team.divisionId === editingMatch.groupId
        );
      }
    });

    // Group matches by date
    const groupedMatches = computed(() => {
      const groups = [];
      const dateMap = {};

      // Sort by date (soonest first)
      const sortedMatches = [...filteredMatches.value].sort((a, b) => {
        const dateA = new Date(a.date);
        const dateB = new Date(b.date);
        return dateA - dateB;
      });

      // Group by date - use date part only to ensure proper grouping
      sortedMatches.forEach((match) => {
        const matchDate = new Date(match.date);
        // Use only the date part for grouping (ignore time)
        const dateString = matchDate.toISOString().split("T")[0];

        if (!dateMap[dateString]) {
          dateMap[dateString] = {
            date: matchDate,
            matches: [],
          };
          groups.push(dateMap[dateString]);
        }

        dateMap[dateString].matches.push(match);
      });

      // Sort matches within each group by time
      groups.forEach((group) => {
        group.matches.sort((a, b) => {
          // Convert time strings to comparable values
          const timeA = a.time ? a.time.replace(":", "") : "9999"; // Default high value for matches without time
          const timeB = b.time ? b.time.replace(":", "") : "9999";
          return timeA.localeCompare(timeB); // Sort times in ascending order
        });
      });

      return groups;
    });

    // Add this new function to generate potential match days
    const generatePotentialMatchDays = () => {
      potentialMatchDays.value = [];

      if (!generateSettings.startDate) return;

      // Get start and end dates
      const startDate = new Date(generateSettings.startDate);
      const endDate = generateSettings.endDate
        ? new Date(generateSettings.endDate)
