    // Updated updateTeamStatistics method for team matches
    const updateTeamStatistics = async (result) => {
      try {
        console.log("Updating team statistics with result:", result);

        // Find the teams to update their stats
        const homeTeam = teams.value.find(
          (t) => t.name === editingMatch.homeTeam
        );
        const awayTeam = teams.value.find(
          (t) => t.name === editingMatch.awayTeam
        );

        if (!homeTeam || !awayTeam) {
          console.error("Could not find teams to update statistics");
          console.log("Home team name:", editingMatch.homeTeam);
          console.log("Away team name:", editingMatch.awayTeam);
          console.log(
            "Available teams:",
            teams.value.map((t) => t.name)
          );
          throw new Error("Teams not found in the database");
        }

        console.log("Found teams:", homeTeam.name, awayTeam.name);

        const homeTeamRef = doc(db, "teams", homeTeam.id);
        const awayTeamRef = doc(db, "teams", awayTeam.id);

        // Get the group to determine points allocation
        const group = groups.value.find((g) => g.id === editingMatch.groupId);
        const pointsForWin = group?.pointsForWin || 3;
        const pointsForDraw = group?.pointsForDraw || 1;

        console.log("Group settings:", {
          groupId: editingMatch.groupId,
          pointsForWin,
          pointsForDraw,
        });

        // Determine the winner based on team match score
        const homeTeamScore = parseInt(result.homeTeamScore) || 0;
        const awayTeamScore = parseInt(result.awayTeamScore) || 0;

        const homeWins = homeTeamScore > awayTeamScore;
        const awayWins = awayTeamScore > homeTeamScore;
        const isDraw = homeTeamScore === awayTeamScore;

        console.log("Match outcome:", {
          homeTeamScore,
          awayTeamScore,
          homeWins,
          awayWins,
          isDraw,
        });

        // Calculate total games won by each team by summing up team points from individual matches
        let homeGamesWon = 0;
        let awayGamesWon = 0;
        let totalGamesPlayed = 0;

        if (
          result.teamMatches &&
          Array.isArray(result.teamMatches) &&
          result.teamMatches.length > 0
        ) {
          console.log("Processing team matches:", result.teamMatches.length);

          result.teamMatches.forEach((match) => {
            // Skip invalid matches
            if (!match) return;

            // Try to get team points if available
            if (match.teamPoints) {
              homeGamesWon += parseInt(match.teamPoints.home || 0);
              awayGamesWon += parseInt(match.teamPoints.away || 0);
            } else {
              // Fallback for older format
              homeGamesWon += match.homeScore > match.awayScore ? 1 : 0;
              awayGamesWon += match.awayScore > match.homeScore ? 1 : 0;
            }

            // Count total individual matches played
            if (match.homePlayer && match.awayPlayer) {
              totalGamesPlayed++;
            }
          });
        } else {
          console.log("No team matches found or invalid format");
          // Fallback to overall match score if no team matches data
          totalGamesPlayed = 1;
          homeGamesWon = homeWins ? 1 : 0;
          awayGamesWon = awayWins ? 1 : 0;
        }

        console.log("Game statistics:", {
          homeGamesWon,
          awayGamesWon,
          totalGamesPlayed,
        });

        // Update home team stats
        const homeTeamUpdate = {
          played: (homeTeam.played || 0) + 1,
          won: (homeTeam.won || 0) + (homeWins ? 1 : 0),
          drawn: (homeTeam.drawn || 0) + (isDraw ? 1 : 0),
          lost: (homeTeam.lost || 0) + (awayWins ? 1 : 0),
          points:
            (homeTeam.points || 0) +
            (homeWins ? pointsForWin : isDraw ? pointsForDraw : 0),
          // Track games won/lost
          gamesPlayed: (homeTeam.gamesPlayed || 0) + totalGamesPlayed,
          gamesWon: (homeTeam.gamesWon || 0) + homeGamesWon,
        };

        console.log("Updating home team with:", homeTeamUpdate);
        await updateDoc(homeTeamRef, homeTeamUpdate);

        // Update away team stats
        const awayTeamUpdate = {
          played: (awayTeam.played || 0) + 1,
          won: (awayTeam.won || 0) + (awayWins ? 1 : 0),
          drawn: (awayTeam.drawn || 0) + (isDraw ? 1 : 0),
