          points:
            (awayTeam.points || 0) +
            (awayWins ? pointsForWin : isDraw ? pointsForDraw : 0),
          // Track games won/lost
          gamesPlayed: (awayTeam.gamesPlayed || 0) + totalGamesPlayed,
          gamesWon: (awayTeam.gamesWon || 0) + awayGamesWon,
        };

        console.log("Updating away team with:", awayTeamUpdate);
        await updateDoc(awayTeamRef, awayTeamUpdate);

        console.log("Team statistics updated successfully");
      } catch (error) {
        console.error("Error updating team statistics:", error);
        throw error;
      }
    };

    // Confirm delete
    const confirmDelete = (match) => {
      matchToDelete.id = match.id;
      matchToDelete.homeTeam = match.homeTeam || match.player1;
      matchToDelete.awayTeam = match.awayTeam || match.player2;
      matchToDelete.date = match.date;

      showDeleteModal.value = true;
    };

    // Delete match
    const deleteMatch = async () => {
      try {
        await deleteDoc(doc(db, "matches", matchToDelete.id));

        // Reload data
        await loadData();

        showDeleteModal.value = false;
        // alert("Match deleted successfully!");
      } catch (error) {
        console.error("Error deleting match:", error);
        alert("Failed to delete match. Please try again.");
      }
    };

    // Generate round-robin schedule
    const generateRoundRobinSchedule = (teams, group) => {
      const matches = [];

      // Create matches for each pair of teams
      for (let i = 0; i < teams.length; i++) {
        for (let j = i + 1; j < teams.length; j++) {
          // Determine home/away based on strategy
          let homeTeam, awayTeam;

          if (generateSettings.homeAwayStrategy === "random") {
            // Random assignment
            if (Math.random() > 0.5) {
              homeTeam = teams[i];
              awayTeam = teams[j];
            } else {
              homeTeam = teams[j];
              awayTeam = teams[i];
            }
          } else {
            // Default: alternate assignment
            homeTeam = teams[i];
            awayTeam = teams[j];
          }

          matches.push({
            homeTeam,
            awayTeam,
            group,
          });
        }
      }

      return matches;
    };

    // Assign dates and times
    const assignDatesAndTimes = (matches) => {
      // Create a copy of matches to avoid modifying original array
      const scheduledMatches = [...matches];

      // Get available times
      const availableTimes = [];
      if (generateSettings.matchTimes["20:00"]) availableTimes.push("20:00");
      if (generateSettings.matchTimes["21:00"]) availableTimes.push("21:00");

      // Filter only selected match days
      const selectedMatchDays = potentialMatchDays.value
        .filter((day) => day.selected)
        .map((day) => day.date);

      if (selectedMatchDays.length === 0) {
        throw new Error(
          "No match days selected. Please select at least one date."
        );
      }

      // Track which teams are already playing on which days and weeks
      const teamMatchDays = {};
      const teamMatchWeeks = {};

      // Default behavior is to limit teams to one match per week, even if setting is toggled off
      const enforceOneMatchPerWeek = true;

      // Track which day in each week has more matches scheduled
      const weekDayMatchCounts = {};

      // Track matches per day/timeslot for scheduling
      const dayTimeSlotCount = {};

      // Track which table pairs are used for each time slot
      const usedTablePairs = {};

      // Maximum matches per timeslot (based on available table pairs)
      const maxMatchesPerTimeslot = Math.floor(maxTables.value / 2);
      console.log(
        `Max matches per timeslot: ${maxMatchesPerTimeslot} (${maxTables.value} tables = ${maxMatchesPerTimeslot} table pairs)`
      );

      // Helper to check if teams are available on a given date
      // eslint-disable-next-line
      const areTeamsAvailableOnDate = (homeTeam, awayTeam, date) => {
        const dateStr = date.toISOString().split("T")[0];

        // Get the year and week number for this date
        const dateObj = new Date(dateStr);
        const weekNumber = getWeekNumber(dateObj);
        const year = dateObj.getFullYear();
        const yearWeekKey = `${year}-${weekNumber}`;

        // Always enforce the one match per week rule by default
        if (
          enforceOneMatchPerWeek ||
          generateSettings.limitOneMatchPerTeamPerWeek
        ) {
          // Check if either team already has a match this week
          if (
            teamMatchWeeks[homeTeam.name]?.has(yearWeekKey) ||
            teamMatchWeeks[awayTeam.name]?.has(yearWeekKey)
          ) {
            return false;
          }
        }

        return true;
      };

      // Function to get team day preferences
      const getTeamDayPreferences = (homeTeam, awayTeam) => {
        let preferredDays = [1, 3]; // Default: both Monday and Wednesday

        if (generateSettings.respectPreferences) {
          // Check home team preferences
          if (homeTeam.preferredDay === "Monday Only") {
            preferredDays = [1];
          } else if (homeTeam.preferredDay === "Wednesday Only") {
            preferredDays = [3];
          }

          // Check away team preferences
          if (awayTeam.preferredDay === "Monday Only") {
            preferredDays = preferredDays.filter((day) => day === 1);
          } else if (awayTeam.preferredDay === "Wednesday Only") {
            preferredDays = preferredDays.filter((day) => day === 3);
          }
        }

        return preferredDays;
      };

      // Group the available dates by week to balance distribution
      const weeklyDates = {};
      selectedMatchDays.forEach((date) => {
        const dateObj = date;
        const weekNumber = getWeekNumber(dateObj);
        const year = dateObj.getFullYear();
        const yearWeekKey = `${year}-${weekNumber}`;

        if (!weeklyDates[yearWeekKey]) {
          weeklyDates[yearWeekKey] = {
            monday: null,
            wednesday: null,
            dates: [],
          };
        }

        // Track Monday and Wednesday separately
        if (dateObj.getDay() === 1) {
          // Monday
          weeklyDates[yearWeekKey].monday = dateObj;
        } else if (dateObj.getDay() === 3) {
          // Wednesday
          weeklyDates[yearWeekKey].wednesday = dateObj;
        }

        weeklyDates[yearWeekKey].dates.push(dateObj);
      });

      // Assign dates and times to matches
      for (let i = 0; i < scheduledMatches.length; i++) {
        const match = scheduledMatches[i];

        // Get team preferences for days
        const preferredDays = getTeamDayPreferences(
          match.homeTeam,
          match.awayTeam
        );

        // Find an available date for this match
        let matchAssigned = false;

        // Get a balanced date that distributes matches evenly between Monday and Wednesday
        const availableDatesByWeek = Object.values(weeklyDates);

        // Sort weeks by date (chronologically)
        availableDatesByWeek.sort((a, b) => {
          return new Date(a.dates[0]) - new Date(b.dates[0]);
        });

        // Try to find a suitable date from the available weeks
        for (const week of availableDatesByWeek) {
          const yearWeekKey = Object.keys(weeklyDates).find(
            (key) => weeklyDates[key] === week
          );

          // Skip if both teams already have a match this week
          const homeTeamHasMatch =
            teamMatchWeeks[match.homeTeam.name]?.has(yearWeekKey);
          const awayTeamHasMatch =
            teamMatchWeeks[match.awayTeam.name]?.has(yearWeekKey);

          if (homeTeamHasMatch || awayTeamHasMatch) {
            continue;
          }

          // Initialize match counts for this week if needed
          if (!weekDayMatchCounts[yearWeekKey]) {
            weekDayMatchCounts[yearWeekKey] = {
              monday: 0,
              wednesday: 0,
            };
          }

          // Check strict day preferences
          const hasStrictPreference = preferredDays.length === 1;

          // Get available days this week based on preferences
          let availableDays = [];

          if (hasStrictPreference) {
            // For teams with strict preferences
            if (preferredDays.includes(1) && week.monday) {
              availableDays.push(week.monday);
            }
            if (preferredDays.includes(3) && week.wednesday) {
              availableDays.push(week.wednesday);
            }
          } else {
            // For teams with flexible preferences
            let mondayCount = weekDayMatchCounts[yearWeekKey].monday;
            let wednesdayCount = weekDayMatchCounts[yearWeekKey].wednesday;

            // Prioritize the day with fewer matches for balanced distribution
            if (week.monday && week.wednesday) {
              if (mondayCount <= wednesdayCount) {
                availableDays = [week.monday, week.wednesday];
              } else {
                availableDays = [week.wednesday, week.monday];
              }
            } else if (week.monday) {
              availableDays = [week.monday];
            } else if (week.wednesday) {
              availableDays = [week.wednesday];
            }
          }

          // Try each available day in this week
          for (const potentialDate of availableDays) {
            // Skip if date is not selected (could have been unchecked by admin)
            if (
              !potentialMatchDays.value.find(
                (day) =>
                  day.selected &&
                  day.date.toISOString().split("T")[0] ===
                    new Date(potentialDate).toISOString().split("T")[0]
              )
            ) {
              continue;
            }

            const dateObj = new Date(potentialDate);
            const dateStr = dateObj.toISOString().split("T")[0];

            // Check available time slots for this date
            for (const timeSlot of availableTimes) {
              const timeSlotKey = `${dateStr}_${timeSlot}`;

              // Initialize counter if needed
              if (!dayTimeSlotCount[timeSlotKey]) {
                dayTimeSlotCount[timeSlotKey] = 0;
              }

              // If this time slot has room for another match
              if (dayTimeSlotCount[timeSlotKey] < maxMatchesPerTimeslot) {
                // Initialize used table pairs tracker for this time slot if needed
                if (!usedTablePairs[timeSlotKey]) {
                  usedTablePairs[timeSlotKey] = new Set();
                }

                // Find an available table pair
                let tablePairIndex = 0;
                while (
                  usedTablePairs[timeSlotKey].has(tablePairIndex) &&
                  tablePairIndex < maxMatchesPerTimeslot
                ) {
                  tablePairIndex++;
                }

                // Assign tables if we found an available pair
                if (tablePairIndex < maxMatchesPerTimeslot) {
                  const tableStart = tablePairIndex * 2 + 1;
                  const tableEnd = tableStart + 1;
                  match.date = new Date(potentialDate);
                  match.time = timeSlot;
                  match.location = `${tableStart} & ${tableEnd}`;

                  // Mark this table pair as used for this time slot
                  usedTablePairs[timeSlotKey].add(tablePairIndex);

                  // Update match count for this time slot
                  if (!dayTimeSlotCount[timeSlotKey]) {
                    dayTimeSlotCount[timeSlotKey] = 0;
                  }
                  dayTimeSlotCount[timeSlotKey]++;

                  // Track that these teams now have a match on this day and in this week
                  const weekNumber = getWeekNumber(dateObj);
                  const year = dateObj.getFullYear();
                  const yearWeekKey = `${year}-${weekNumber}`;

                  // Initialize team's match tracking if needed
                  if (!teamMatchDays[match.homeTeam.name]) {
                    teamMatchDays[match.homeTeam.name] = [];
                  }
                  if (!teamMatchDays[match.awayTeam.name]) {
                    teamMatchDays[match.awayTeam.name] = [];
                  }

                  // Initialize week tracking for teams
                  if (!teamMatchWeeks[match.homeTeam.name]) {
                    teamMatchWeeks[match.homeTeam.name] = new Set();
                  }
                  if (!teamMatchWeeks[match.awayTeam.name]) {
                    teamMatchWeeks[match.awayTeam.name] = new Set();
                  }

                  // Record that the teams have a match on this date and in this week
                  teamMatchDays[match.homeTeam.name].push(dateStr);
                  teamMatchDays[match.awayTeam.name].push(dateStr);
                  teamMatchWeeks[match.homeTeam.name].add(yearWeekKey);
                  teamMatchWeeks[match.awayTeam.name].add(yearWeekKey);

                  matchAssigned = true;

                  // Update the match count for this day in this week
                  if (dateObj.getDay() === 1) {
                    // Monday
                    if (!weekDayMatchCounts[yearWeekKey]) {
                      weekDayMatchCounts[yearWeekKey] = {
                        monday: 0,
                        wednesday: 0,
                      };
                    }
                    weekDayMatchCounts[yearWeekKey].monday += 1;
                  } else if (dateObj.getDay() === 3) {
                    // Wednesday
                    if (!weekDayMatchCounts[yearWeekKey]) {
                      weekDayMatchCounts[yearWeekKey] = {
                        monday: 0,
                        wednesday: 0,
                      };
                    }
                    weekDayMatchCounts[yearWeekKey].wednesday += 1;
                  }

                  break;
                } else {
                  // No available table pairs for this time slot
                  continue; // Try the next time slot
                }
              }
            }

            if (matchAssigned) break;
          }

          if (matchAssigned) break;
        }

        // If no date could be assigned, mark the match with a warning
        if (!matchAssigned) {
          match.date = null;
          match.time = "SCHEDULING CONFLICT";
          match.location = "Unable to schedule - no available slot";
          console.warn(
            `Could not schedule match between ${match.homeTeam.name} and ${match.awayTeam.name}`
          );
        }
      }

      return scheduledMatches;
    };

    // Generate matches
    const generateMatches = async () => {
      if (!generateSettings.groupId) {
        alert("Please select a group/division");
        return;
      }

      if (!generateSettings.startDate) {
        alert("Please select a start date");
        return;
      }

      if (
        !generateSettings.matchDays.monday &&
        !generateSettings.matchDays.wednesday
      ) {
        alert("Please select at least one match day");
        return;
      }

      if (
        !generateSettings.matchTimes["20:00"] &&
        !generateSettings.matchTimes["21:00"]
      ) {
        alert("Please select at least one match time");
        return;
      }

      // Check if we have any selected match days
      const selectedDays = potentialMatchDays.value.filter(
        (day) => day.selected
      );
      if (selectedDays.length === 0) {
        alert("Please select at least one specific match date");
        return;
      }

      try {
        isGenerating.value = true;
        console.log("Starting match generation...");

        // Step 1: Get groups to generate matches for
        let groupsToProcess = [];

        if (generateSettings.groupId === "all") {
          // Get all groups for this phase
          console.log(`Getting all groups for phase: ${props.phase}`);
          groupsToProcess = [...groups.value];
        } else {
          // Just the selected group
          const group = groups.value.find(
            (g) => g.id === generateSettings.groupId
          );

          if (group) {
            groupsToProcess.push(group);
          } else {
            throw new Error("Selected group not found");
          }
        }

        console.log(`Processing ${groupsToProcess.length} groups`);

        // Step 2: For each group, get teams and generate matches
        let totalMatchesGenerated = 0;
        let unscheduledMatches = 0;

        for (const group of groupsToProcess) {
          console.log(`Generating matches for group: ${group.name}`);

          // Get teams in this group
          let teamsInGroup = [];

          if (props.phase === "qualification") {
            teamsInGroup = teams.value.filter(
              (team) => team.groupId === group.id
            );
          } else {
            teamsInGroup = teams.value.filter(
              (team) => team.divisionId === group.id
            );
          }

          console.log(
            `Found ${teamsInGroup.length} teams in group ${group.name}`
          );

          // Skip if not enough teams
          if (teamsInGroup.length < 2) {
            console.log(`Skipping group ${group.name}: not enough teams`);
            continue;
          }

          // Step 3: Generate round-robin pairings
          const matches = generateRoundRobinSchedule(teamsInGroup, group);
          console.log(`Generated ${matches.length} match pairings`);

          // Step 4: Assign dates and times based on preferences
          const scheduledMatches = assignDatesAndTimes(matches);

          // Count unscheduled matches
          const unscheduled = scheduledMatches.filter(
            (match) => match.date === null
          ).length;
          unscheduledMatches += unscheduled;

          if (unscheduled > 0) {
            console.warn(
              `${unscheduled} matches could not be scheduled for group ${group.name}`
            );
          }

          // Step 5: Create matches in Firestore
          const matchPromises = scheduledMatches
            .filter((match) => match.date !== null) // Only save matches that could be scheduled
            .map((match) =>
              addDoc(collection(db, "matches"), {
                homeTeam: match.homeTeam.name,
                awayTeam: match.awayTeam.name,
                player1: match.homeTeam.name, // For backwards compatibility
                player2: match.awayTeam.name, // For backwards compatibility
                date: match.date,
                time: match.time,
                groupId: group.id,
                phase: props.phase,
                groupType: props.phase, // For backwards compatibility
                groupName: group.name,
                location: match.location || generateSettings.location,
                isCompleted: false,
                createdAt: serverTimestamp(),
              })
            );

          await Promise.all(matchPromises);
          totalMatchesGenerated += scheduledMatches.filter(
            (match) => match.date !== null
          ).length;
        }

        // Success message with info about unscheduled matches
        if (unscheduledMatches > 0) {
          alert(
            `Generated ${totalMatchesGenerated} matches successfully.\n\nWarning: ${unscheduledMatches} matches could not be scheduled due to constraints. You will need to schedule these manually.`
          );
        } else {
          alert(`Successfully generated ${totalMatchesGenerated} matches!`);
        }

        showGenerateModal.value = false;

        // Reload matches
        await loadData();
      } catch (error) {
        console.error("Error generating matches:", error);
        alert(`Error generating matches: ${error.message}`);
      } finally {
        isGenerating.value = false;
      }
    };

    // Initialize component
    onMounted(() => {
      loadData();
    });

    // Reset newMatch.phase when phase prop changes
    watch(
      () => props.phase,
      (newPhase) => {
        console.log(`Phase changed to: ${newPhase}`);
        newMatch.phase = newPhase;
        // Reload data to match the new phase
        loadData();
      }
    );

    // Watch to update potential match days when the generate modal is shown
    watch(
      () => showGenerateModal.value,
      (isShown) => {
        if (isShown) {
          generatePotentialMatchDays();
        }
      }
    );

    return {
      matches,
      teams,
      groups,
      filteredMatches,
      filteredTeams,
      teamsForEditGroup,
      groupedMatches,
      loading,
      error,
      lastUpdated,
      groupFilter,
      formatDate,
      formatDateHeader,
      showEditModal,
      showDeleteModal,
      showResultModal,
      showScheduleModal,
      showGenerateModal,
      showDeleteAllModal,
      isGenerating,
      maxTables,
      editingMatch,
      matchToDelete,
      newMatch,
      generateSettings,
      potentialMatchDays,
      generatePotentialMatchDays,
      editMatch,
      updateMatch,
      assignResult,
      saveMatchResult,
      scheduleMatch,
      confirmDelete,
      deleteMatch,
      generateMatches,
      confirmDeleteAll,
      deleteAllMatches,
    };
  },
};
</script>

