<template>
  <div class="team-match-result-dialog">
    <div class="modal-overlay" v-if="showResultModal">
      <div class="modal-content large-modal">
        <div class="modal-header">
          <h3>Add Team Match Results</h3>
          <button @click="cancelAndClose" class="close-btn">×</button>
        </div>

        <div class="modal-body">
          <!-- Match information header -->
          <div class="match-teams-header">
            <div class="match-info">
              <div class="match-date">
                {{ formatDate(matchData.date) }} | {{ matchData.time || "TBD" }}
              </div>
              <div class="match-teams">
                {{ matchData.homeTeam }} 🆚 {{ matchData.awayTeam }}
              </div>
              <div class="location" v-if="matchData.location">
                Tables: {{ matchData.location }}
              </div>
            </div>
          </div>

          <!-- Full Team Match Form -->
          <div class="team-match-form">
            <!-- Header Row -->
            <div class="form-row header-row">
              <div class="match-cell">Match</div>
              <div class="player-cell">Home Player</div>
              <div class="player-cell">Away Player</div>
              <div class="game-cell">Game 1</div>
              <div class="game-cell">Game 2</div>
              <div class="game-cell">Game 3</div>
              <div class="game-cell">Game 4</div>
              <div class="game-cell">Game 5</div>
              <div class="score-cell">Match score</div>
              <div class="team-score-cell">Team score</div>
            </div>

            <!-- Match S1 -->
            <div class="form-row match-row">
              <div class="match-cell">S1</div>
              <div class="player-cell">
                <select
                  v-model="teamMatch.matches[0].homePlayer"
                  class="player-select"
                >
                  <option value="">-- Select player --</option>
                  <option
                    v-for="(player, idx) in homeTeamPlayers"
                    :key="`home-s1-${idx}-${player.firstName || idx}`"
                    :value="player.firstName"
                  >
                    {{ player.firstName }}
                  </option>
                </select>
              </div>
              <div class="player-cell">
                <select
                  v-model="teamMatch.matches[0].awayPlayer"
                  class="player-select"
                >
                  <option value="">-- Select player --</option>
                  <option
                    v-for="(player, idx) in awayTeamPlayers"
                    :key="`away-s1-${idx}-${player.firstName || idx}`"
                    :value="player.firstName"
                  >
                    {{ player.firstName }}
                  </option>
                </select>
              </div>
              <div
                class="game-cell"
                v-for="gameIndex in 5"
                :key="`s1-game-${gameIndex}`"
              >
                <div class="game-score-inputs">
                  <input
                    type="number"
                    v-model="teamMatch.matches[0].games[gameIndex - 1].home"
                    class="score-input"
                    min="0"
                    max="99"
                    @input="updateMatchScore(0)"
                  />
                  <span class="score-separator">-</span>
                  <input
                    type="number"
                    v-model="teamMatch.matches[0].games[gameIndex - 1].away"
                    class="score-input"
                    min="0"
                    max="99"
                    @input="updateMatchScore(0)"
                  />
                </div>
              </div>
              <div class="score-cell match-score">
                {{ teamMatch.matches[0].homeScore }}-{{
                  teamMatch.matches[0].awayScore
                }}
              </div>
              <div class="team-score-cell">
                <div
                  class="team-points-display"
                  v-if="cumulativeScores[0] && cumulativeScores[0].completed"
                >
                  {{ cumulativeScores[0].home }}
                  <span class="score-separator">-</span>
                  {{ cumulativeScores[0].away }}
                </div>
                <div class="team-points-display" v-else>-</div>
              </div>
            </div>

            <!-- Match S2 -->
            <div class="form-row match-row">
              <div class="match-cell">S2</div>
              <div class="player-cell">
                <select
                  v-model="teamMatch.matches[1].homePlayer"
                  class="player-select"
                >
                  <option value="">-- Select player --</option>
                  <option
                    v-for="(player, idx) in homeTeamPlayers"
                    :key="`home-s2-${idx}-${player.firstName || idx}`"
                    :value="player.firstName"
                  >
                    {{ player.firstName }}
                  </option>
                </select>
              </div>
              <div class="player-cell">
                <select
                  v-model="teamMatch.matches[1].awayPlayer"
                  class="player-select"
                >
                  <option value="">-- Select player --</option>
                  <option
                    v-for="(player, idx) in awayTeamPlayers"
                    :key="`away-s2-${idx}-${player.firstName || idx}`"
                    :value="player.firstName"
                  >
                    {{ player.firstName }}
                  </option>
                </select>
              </div>
              <div
                class="game-cell"
                v-for="gameIndex in 5"
                :key="`s2-game-${gameIndex}`"
              >
                <div class="game-score-inputs">
                  <input
                    type="number"
                    v-model="teamMatch.matches[1].games[gameIndex - 1].home"
                    class="score-input"
                    min="0"
                    max="99"
                    @input="updateMatchScore(1)"
                  />
                  <span class="score-separator">-</span>
                  <input
                    type="number"
                    v-model="teamMatch.matches[1].games[gameIndex - 1].away"
                    class="score-input"
                    min="0"
                    max="99"
                    @input="updateMatchScore(1)"
                  />
                </div>
              </div>
              <div class="score-cell match-score">
                {{ teamMatch.matches[1].homeScore }}-{{
                  teamMatch.matches[1].awayScore
                }}
              </div>
              <div class="team-score-cell">
                <div
                  class="team-points-display"
                  v-if="cumulativeScores[1] && cumulativeScores[1].completed"
                >
                  {{ cumulativeScores[1].home }}
                  <span class="score-separator">-</span>
                  {{ cumulativeScores[1].away }}
                </div>
                <div class="team-points-display" v-else>-</div>
              </div>
            </div>

            <!-- Match Dbl (Doubles) -->
            <div class="form-row match-row doubles-row">
              <div class="match-cell">Dbl</div>
              <div class="player-cell">
                <div class="doubles-players">
                  <select
                    v-model="teamMatch.matches[2].homePlayer"
                    class="player-select"
                  >
                    <option value="">-- Select player 1 --</option>
                    <option
                      v-for="(player, idx) in homeTeamPlayers"
                      :key="`home-dbl1-${idx}-${player.firstName || idx}`"
                      :value="player.firstName"
                    >
                      {{ player.firstName }}
                    </option>
                  </select>
                  <select
                    v-model="teamMatch.matches[2].homePartner"
                    class="player-select"
                  >
                    <option value="">-- Select player 2 --</option>
                    <option
                      v-for="(player, idx) in homeTeamPlayers"
                      :key="`home-dbl2-${idx}-${player.firstName || idx}`"
                      :value="player.firstName"
                      :disabled="
                        player.firstName === teamMatch.matches[2].homePlayer
                      "
                    >
                      {{ player.firstName }}
                    </option>
                  </select>
                </div>
              </div>
              <div class="player-cell">
                <div class="doubles-players">
                  <select
                    v-model="teamMatch.matches[2].awayPlayer"
                    class="player-select"
                  >
                    <option value="">-- Select player 1 --</option>
                    <option
                      v-for="(player, idx) in awayTeamPlayers"
                      :key="`away-dbl1-${idx}-${player.firstName || idx}`"
                      :value="player.firstName"
                    >
                      {{ player.firstName }}
                    </option>
                  </select>
                  <select
                    v-model="teamMatch.matches[2].awayPartner"
                    class="player-select"
                  >
                    <option value="">-- Select player 2 --</option>
                    <option
                      v-for="(player, idx) in awayTeamPlayers"
                      :key="`away-dbl2-${idx}-${player.firstName || idx}`"
                      :value="player.firstName"
                      :disabled="
                        player.firstName === teamMatch.matches[2].awayPlayer
                      "
                    >
                      {{ player.firstName }}
                    </option>
                  </select>
                </div>
              </div>
              <div
                class="game-cell"
                v-for="gameIndex in 5"
                :key="`dbl-game-${gameIndex}`"
              >
                <div class="game-score-inputs">
                  <input
                    type="number"
                    v-model="teamMatch.matches[2].games[gameIndex - 1].home"
                    class="score-input"
                    min="0"
                    max="99"
                    @input="updateMatchScore(2)"
                  />
                  <span class="score-separator">-</span>
                  <input
                    type="number"
                    v-model="teamMatch.matches[2].games[gameIndex - 1].away"
                    class="score-input"
                    min="0"
                    max="99"
                    @input="updateMatchScore(2)"
                  />
                </div>
              </div>
              <div class="score-cell match-score">
                {{ teamMatch.matches[2].homeScore }}-{{
                  teamMatch.matches[2].awayScore
                }}
              </div>
              <div class="team-score-cell">
                <div
                  class="team-points-display"
                  v-if="cumulativeScores[2] && cumulativeScores[2].completed"
                >
                  {{ cumulativeScores[2].home }}
                  <span class="score-separator">-</span>
                  {{ cumulativeScores[2].away }}
                </div>
                <div class="team-points-display" v-else>-</div>
              </div>
            </div>

            <!-- Match S3 -->
            <div class="form-row match-row">
              <div class="match-cell">S3</div>
              <div class="player-cell">
                <select
                  v-model="teamMatch.matches[3].homePlayer"
                  class="player-select"
                >
                  <option value="">-- Select player --</option>
                  <option
                    v-for="(player, idx) in homeTeamPlayers"
                    :key="`home-s3-${idx}-${player.firstName || idx}`"
                    :value="player.firstName"
                  >
                    {{ player.firstName }}
                  </option>
                </select>
              </div>
              <div class="player-cell">
                <select
                  v-model="teamMatch.matches[3].awayPlayer"
                  class="player-select"
                >
                  <option value="">-- Select player --</option>
                  <option
                    v-for="(player, idx) in awayTeamPlayers"
                    :key="`away-s3-${idx}-${player.firstName || idx}`"
                    :value="player.firstName"
                  >
                    {{ player.firstName }}
                  </option>
                </select>
              </div>
              <div
                class="game-cell"
                v-for="gameIndex in 5"
                :key="`s3-game-${gameIndex}`"
              >
                <div class="game-score-inputs">
                  <input
                    type="number"
                    v-model="teamMatch.matches[3].games[gameIndex - 1].home"
                    class="score-input"
                    min="0"
                    max="99"
                    @input="updateMatchScore(3)"
                  />
                  <span class="score-separator">-</span>
                  <input
                    type="number"
                    v-model="teamMatch.matches[3].games[gameIndex - 1].away"
                    class="score-input"
                    min="0"
                    max="99"
                    @input="updateMatchScore(3)"
                  />
                </div>
              </div>
              <div class="score-cell match-score">
                {{ teamMatch.matches[3].homeScore }}-{{
                  teamMatch.matches[3].awayScore
                }}
              </div>
              <div class="team-score-cell">
                <div
                  class="team-points-display"
                  v-if="cumulativeScores[3] && cumulativeScores[3].completed"
                >
                  {{ cumulativeScores[3].home }}
                  <span class="score-separator">-</span>
                  {{ cumulativeScores[3].away }}
                </div>
                <div class="team-points-display" v-else>-</div>
              </div>
            </div>

            <!-- Match S4 -->
            <div class="form-row match-row">
              <div class="match-cell">S4</div>
              <div class="player-cell">
                <select
                  v-model="teamMatch.matches[4].homePlayer"
                  class="player-select"
                >
                  <option value="">-- Select player --</option>
                  <option
                    v-for="(player, idx) in homeTeamPlayers"
                    :key="`home-s4-${idx}-${player.firstName || idx}`"
                    :value="player.firstName"
                  >
                    {{ player.firstName }}
                  </option>
                </select>
              </div>
              <div class="player-cell">
                <select
                  v-model="teamMatch.matches[4].awayPlayer"
                  class="player-select"
                >
                  <option value="">-- Select player --</option>
                  <option
                    v-for="(player, idx) in awayTeamPlayers"
                    :key="`away-s4-${idx}-${player.firstName || idx}`"
                    :value="player.firstName"
                  >
                    {{ player.firstName }}
                  </option>
                </select>
              </div>
              <div
                class="game-cell"
                v-for="gameIndex in 5"
                :key="`s4-game-${gameIndex}`"
              >
                <div class="game-score-inputs">
                  <input
                    type="number"
                    v-model="teamMatch.matches[4].games[gameIndex - 1].home"
                    class="score-input"
                    min="0"
                    max="99"
                    @input="updateMatchScore(4)"
                  />
                  <span class="score-separator">-</span>
                  <input
                    type="number"
                    v-model="teamMatch.matches[4].games[gameIndex - 1].away"
                    class="score-input"
                    min="0"
                    max="99"
                    @input="updateMatchScore(4)"
                  />
                </div>
              </div>
              <div class="score-cell match-score">
                {{ teamMatch.matches[4].homeScore }}-{{
                  teamMatch.matches[4].awayScore
                }}
              </div>
              <div class="team-score-cell">
                <div
                  class="team-points-display"
                  v-if="cumulativeScores[4] && cumulativeScores[4].completed"
                >
                  {{ cumulativeScores[4].home }}
                  <span class="score-separator">-</span>
                  {{ cumulativeScores[4].away }}
                </div>
                <div class="team-points-display" v-else>-</div>
              </div>
            </div>

            <!-- Team Match Total Score -->
            <div class="form-row total-row">
              <div class="match-cell">Total</div>
              <div class="player-cell"></div>
              <div class="player-cell"></div>
              <div class="game-cell"></div>
              <div class="game-cell"></div>
              <div class="game-cell"></div>
              <div class="game-cell"></div>
              <div class="game-cell"></div>
              <div class="score-cell"></div>
              <!-- <div class="team-score-cell total-score">
                <div class="total-score-display">
                  <span class="home-total">{{ totalScores.home }}</span>
                  <span class="score-separator">-</span>
                  <span class="away-total">{{ totalScores.away }}</span>
                </div>
              </div> -->
              <div class="team-score-cell total-score">
                <div class="total-score-display">
                  <span class="home-total">{{ totalScores.home }}</span>
                  <span class="score-separator">-</span>
                  <span class="away-total">{{ totalScores.away }}</span>
                </div>
              </div>
            </div>
          </div>

          <!-- Error messages -->
          <div class="error-message" v-if="validationError">
            {{ validationError }}
          </div>
        </div>

        <div class="modal-footer">
          <button @click="saveTeamMatchResult" class="btn save-btn">
            Save Results
          </button>
          <button @click="cancelAndClose" class="btn cancel-btn">Cancel</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "TeamMatchResultForm",

  props: {
    showResultModal: {
      type: Boolean,
      default: false,
    },
    matchData: {
      type: Object,
      default: () => ({
        id: "",
        homeTeam: "",
        awayTeam: "",
        date: new Date(),
        time: "",
        location: "",
        groupId: "",
        phase: "qualification",
      }),
    },
    teams: {
      type: Array,
      default: () => [],
    },
  },

  data() {
    return {
      validationError: "",
      teamMatch: {
        homeTeam: "",
        awayTeam: "",
        homeTeamScore: 0,
        awayTeamScore: 0,
        cumulativeHomeScore: 0,
        cumulativeAwayScore: 0,
        matches: [
          // S1
          {
            matchId: "S1",
            type: "singles",
            homePlayer: "",
            awayPlayer: "",
            games: [
              { home: null, away: null },
              { home: null, away: null },
              { home: null, away: null },
              { home: null, away: null },
              { home: null, away: null },
            ],
            homeScore: 0,
            awayScore: 0,
            teamPoints: { home: 0, away: 0 },
            orderIndex: 0,
          },
          // S2
          {
            matchId: "S2",
            type: "singles",
            homePlayer: "",
            awayPlayer: "",
            games: [
              { home: null, away: null },
              { home: null, away: null },
              { home: null, away: null },
              { home: null, away: null },
              { home: null, away: null },
            ],
            homeScore: 0,
            awayScore: 0,
            teamPoints: { home: 0, away: 0 },
            orderIndex: 1,
          },
          // Dbl
          {
            matchId: "Dbl",
            type: "doubles",
            homePlayer: "",
            homePartner: "",
            awayPlayer: "",
            awayPartner: "",
            games: [
              { home: null, away: null },
              { home: null, away: null },
              { home: null, away: null },
              { home: null, away: null },
              { home: null, away: null },
            ],
            homeScore: 0,
            awayScore: 0,
            teamPoints: { home: 0, away: 0 },
            orderIndex: 2,
          },
          // S3
          {
            matchId: "S3",
            type: "singles",
            homePlayer: "",
            awayPlayer: "",
            games: [
              { home: null, away: null },
              { home: null, away: null },
              { home: null, away: null },
              { home: null, away: null },
              { home: null, away: null },
            ],
            homeScore: 0,
            awayScore: 0,
            teamPoints: { home: 0, away: 0 },
            orderIndex: 3,
          },
          // S4
          {
            matchId: "S4",
            type: "singles",
            homePlayer: "",
            awayPlayer: "",
            games: [
              { home: null, away: null },
              { home: null, away: null },
              { home: null, away: null },
              { home: null, away: null },
              { home: null, away: null },
            ],
            homeScore: 0,
            awayScore: 0,
            teamPoints: { home: 0, away: 0 },
            orderIndex: 4,
          },
        ],
      },
    };
  },

  computed: {
    homeTeamPlayers() {
      const homeTeam = this.teams.find(
        (team) => team.name === this.matchData.homeTeam
      );
      return homeTeam?.members || [];
    },

    awayTeamPlayers() {
      const awayTeam = this.teams.find(
        (team) => team.name === this.matchData.awayTeam
      );
      return awayTeam?.members || [];
    },

    // Computed property to calculate total team scores
    totalScores() {
      let homeTotal = 0;
      let awayTotal = 0;

      this.teamMatch.matches.forEach((match) => {
        homeTotal += parseInt(match.teamPoints.home) || 0;
        awayTotal += parseInt(match.teamPoints.away) || 0;
      });

      return { home: homeTotal, away: awayTotal };
    },

    // Compute cumulative scores for each match
    cumulativeScores() {
      let results = [];
      let homeRunningTotal = 0;
      let awayRunningTotal = 0;

      // Process matches in their visual order (S1, S2, Dbl, S3, S4)
      const matchOrder = ["S1", "S2", "Dbl", "S3", "S4"];

      for (const matchId of matchOrder) {
        const match = this.teamMatch.matches.find((m) => m.matchId === matchId);
        if (!match) continue;

        // Only add to running total if a winner has been determined
        if (match.homeScore >= 3 || match.awayScore >= 3) {
          homeRunningTotal += parseInt(match.teamPoints.home) || 0;
          awayRunningTotal += parseInt(match.teamPoints.away) || 0;
        }

        results.push({
          matchId: match.matchId,
          home: homeRunningTotal,
          away: awayRunningTotal,
          completed: match.homeScore >= 3 || match.awayScore >= 3,
        });
      }

      return results;
    },
  },

  watch: {
    showResultModal(newVal) {
      if (newVal) {
        console.log("MatchResultForm modal opened with data:", this.matchData);
        this.initializeForm();
      }
    },
    matchData: {
      handler(newVal) {
        console.log("matchData changed:", newVal);
        if (this.showResultModal) {
          console.log("Reinitializing form due to matchData change");
          this.initializeForm();
        }
      },
      deep: true,
    },
  },

  methods: {
    formatDate(dateString) {
      if (!dateString) return "";

      const date = new Date(dateString);
      return date.toLocaleDateString("en-US", {
        weekday: "short",
        month: "short",
        day: "numeric",
      });
    },

    initializeForm() {
      // Reset the form to initial state
      this.validationError = "";

      // Setup basic match data
      this.teamMatch.homeTeam = this.matchData.homeTeam;
      this.teamMatch.awayTeam = this.matchData.awayTeam;

      // If there are existing results, load them
      if (this.matchData.teamMatches && this.matchData.teamMatches.length > 0) {
        // Load existing team match data
        this.loadExistingTeamMatch();
      } else {
        // Reset all matches to default state
        this.resetTeamMatch();
      }

      // Set team total scores
      this.updateTotalTeamScore();
    },

    resetTeamMatch() {
      // Reset all individual matches
      for (let i = 0; i < this.teamMatch.matches.length; i++) {
        const matchType = i === 2 ? "doubles" : "singles";
        const matchId = i === 2 ? "Dbl" : `S${i < 2 ? i + 1 : i}`;

        this.teamMatch.matches[i] = {
          matchId: matchId,
          type: matchType,
          homePlayer: "",
          awayPlayer: "",
          homePartner: i === 2 ? "" : undefined,
          awayPartner: i === 2 ? "" : undefined,
          games: [
            { home: null, away: null },
            { home: null, away: null },
            { home: null, away: null },
            { home: null, away: null },
            { home: null, away: null },
          ],
          homeScore: 0,
          awayScore: 0,
          teamPoints: { home: 0, away: 0 },
          orderIndex: i,
        };
      }
    },

    loadExistingTeamMatch() {
      console.log(
        "Loading existing team match data",
        this.matchData.teamMatches
      );

      if (
        !this.matchData.teamMatches ||
        !Array.isArray(this.matchData.teamMatches)
      ) {
        console.error("No valid team matches data to load");
        return;
      }

      // First ensure we have the standard structure in place
      this.resetTeamMatch();

      // Create a mapping of matchId to index to find matches by ID
      const matchTypes = ["S1", "S2", "Dbl", "S3", "S4"];
      const indexMap = {};
      matchTypes.forEach((type, index) => {
        indexMap[type] = index;
      });

      // Map existing data to the current form structure
      this.matchData.teamMatches.forEach((existingMatch) => {
        if (!existingMatch.matchId) return;

        // Find the corresponding match in our form
        const index = indexMap[existingMatch.matchId];
        if (index === undefined) {
          console.warn(`Unrecognized match ID: ${existingMatch.matchId}`);
          return;
        }

        // Map the data
        const formMatch = this.teamMatch.matches[index];

        // Map the basic info
        formMatch.homePlayer = existingMatch.homePlayer || "";
        formMatch.awayPlayer = existingMatch.awayPlayer || "";

        // For doubles match, also map partners
        if (existingMatch.matchId === "Dbl") {
          formMatch.homePartner = existingMatch.homePartner || "";
          formMatch.awayPartner = existingMatch.awayPartner || "";
        }

        // Map game scores
        if (existingMatch.games && Array.isArray(existingMatch.games)) {
          // For each game in the match
          for (
            let i = 0;
            i < Math.min(formMatch.games.length, existingMatch.games.length);
            i++
          ) {
            formMatch.games[i].home = existingMatch.games[i].home;
            formMatch.games[i].away = existingMatch.games[i].away;
          }
        }

        // Map scores and team points
        formMatch.homeScore = existingMatch.homeScore || 0;
        formMatch.awayScore = existingMatch.awayScore || 0;

        if (existingMatch.teamPoints) {
          formMatch.teamPoints.home = existingMatch.teamPoints.home || 0;
          formMatch.teamPoints.away = existingMatch.teamPoints.away || 0;
        }
      });

      // Update match scores based on games data
      for (let i = 0; i < this.teamMatch.matches.length; i++) {
        this.updateMatchScore(i);
      }

      // Update total team score
      this.teamMatch.homeTeam = this.matchData.homeTeam;
      this.teamMatch.awayTeam = this.matchData.awayTeam;
      this.teamMatch.homeTeamScore = this.matchData.homeTeamScore || 0;
      this.teamMatch.awayTeamScore = this.matchData.awayTeamScore || 0;

      // Force update to refresh UI
      this.$forceUpdate();

      console.log("Loaded existing team match data", this.teamMatch.matches);
    },

    updateMatchScore(matchIndex) {
      const match = this.teamMatch.matches[matchIndex];
      let homeWins = 0;
      let awayWins = 0;

      // Count wins for each player
      match.games.forEach((game) => {
        if (!game.home && !game.away) return;

        const homeScore = parseInt(game.home) || 0;
        const awayScore = parseInt(game.away) || 0;

        if (homeScore > awayScore) {
          homeWins++;
        } else if (awayScore > homeScore) {
          awayWins++;
        }
      });

      // Update match score
      match.homeScore = homeWins;
      match.awayScore = awayWins;

      // Auto-update team points based on match winner (best of 5 system)
      // In best of 5, a player needs 3 wins to win the match
      if (homeWins >= 3) {
        match.teamPoints.home = 1;
        match.teamPoints.away = 0;
      } else if (awayWins >= 3) {
        match.teamPoints.home = 0;
        match.teamPoints.away = 1;
      } else {
        // If no one has reached 3 wins yet, no team points awarded
        match.teamPoints.home = 0;
        match.teamPoints.away = 0;
      }

      // Ensure match score reflects best-of-5 system
      // Maximum is 3 wins (3-0, 3-1, or 3-2)
      if (homeWins > 3) {
        match.homeScore = 3;
      }
      if (awayWins > 3) {
        match.awayScore = 3;
      }

      // Force reactive update
      this.$forceUpdate();
    },

    // No longer needed as we use the computed property
    updateTotalTeamScore() {
      // This is now handled by the computed value calculateTotalTeamScore
    },

    // This method is replaced by a computed property
    calculateTotalTeamScore(team) {
      let total = 0;
      for (const match of this.teamMatch.matches) {
        total += parseInt(match.teamPoints[team]) || 0;
      }
      return total;
    },

    validateForm() {
      // Reset validation error
      this.validationError = "";

      // Check if at least one match has complete results
      let hasCompleteMatch = false;

      for (const match of this.teamMatch.matches) {
        // Check if this match has player selections and at least one game score
        const hasPlayers = match.homePlayer && match.awayPlayer;
        if (match.type === "doubles") {
          // For doubles, we need partners too
          if (!match.homePartner || !match.awayPartner) {
            continue;
          }
        }

        if (!hasPlayers) continue;

        // Check if at least one game has scores
        const hasGameScores = match.games.some(
          (game) =>
            game.home !== null &&
            game.away !== null &&
            game.home !== "" &&
            game.away !== ""
        );

        if (hasGameScores) {
          hasCompleteMatch = true;
          break;
        }
      }

      if (!hasCompleteMatch) {
        this.validationError =
          "At least one match must have players selected and game scores entered";
        return false;
      }

      return true;
    },

    saveTeamMatchResult() {
      if (!this.validateForm()) {
        return;
      }

      // Prepare result data - only include matches that have players and scores
      const completedMatches = this.teamMatch.matches.filter((match) => {
        // Check if this match has player selections
        const hasPlayers = match.homePlayer && match.awayPlayer;
        if (
          match.type === "doubles" &&
          (!match.homePartner || !match.awayPartner)
        ) {
          return false;
        }

        if (!hasPlayers) return false;

        // Check if at least one game has scores
        return match.games.some(
          (game) =>
            game.home !== null &&
            game.away !== null &&
            game.home !== "" &&
            game.away !== ""
        );
      });

      // Map the matches to a format suitable for storage
      const cleanedMatches = completedMatches.map((match) => {
        // Filter out games with no scores
        const validGames = match.games.filter(
          (game) =>
            game.home !== null &&
            game.away !== null &&
            game.home !== "" &&
            game.away !== ""
        );

        return {
          matchId: match.matchId,
          type: match.type,
          homePlayer: match.homePlayer,
          awayPlayer: match.awayPlayer,
          homePartner: match.type === "doubles" ? match.homePartner : undefined,
          awayPartner: match.type === "doubles" ? match.awayPartner : undefined,
          games: validGames,
          homeScore: match.homeScore,
          awayScore: match.awayScore,
          teamPoints: match.teamPoints,
        };
      });

      // Create final result data
      const resultData = {
        homeTeam: this.teamMatch.homeTeam,
        awayTeam: this.teamMatch.awayTeam,
        homeTeamScore: this.calculateTotalTeamScore("home"),
        awayTeamScore: this.calculateTotalTeamScore("away"),
        teamMatches: cleanedMatches,
        isCompleted: true,
        completedDate: new Date(),
      };

      // Emit event with result data
      this.$emit("save-result", {
        matchId: this.matchData.id,
        resultData,
      });

      // Close modal
      this.$emit("close");
    },

    cancelAndClose() {
      this.$emit("close");
    },
  },
};
</script>

