<template>
  <div class="standings-container">
    <div v-if="loading" class="loading-container">
      <div class="loading-spinner"></div>
      <p>Loading standings...</p>
    </div>

    <div v-else-if="error" class="error-container">
      <i class="error-icon">!</i>
      <p>{{ error }}</p>
    </div>

    <div v-else class="standings-content">
      <div class="grouped-standings">
        <div
          v-if="phase === 'qualification' && standingsByGroup.length === 0"
          class="no-teams-message"
        >
          No teams found in any qualification groups
        </div>
        <div
          v-else-if="phase === 'division' && standingsByDivision.length === 0"
          class="no-teams-message"
        >
          No teams found in any divisions
        </div>

        <div
          v-for="group in phase === 'qualification'
            ? standingsByGroup
            : standingsByDivision"
          :key="group.id"
          class="group-standings"
        >
          <h3>{{ group.name }}</h3>

          <div class="standings-table-wrapper">
            <table class="standings-table">
              <thead>
                <tr>
                  <th class="position-col">Rank</th>
                  <th class="player-col">Team</th>
                  <th class="stat-col">Played</th>
                  <th class="stat-col">Won</th>
                  <th class="stat-col">Lost</th>
                  <th class="stat-col points-col">Points</th>
                </tr>
              </thead>
              <tbody>
                <tr
                  v-for="(team, index) in group.teams"
                  :key="team.id"
                  :class="{ 'top-three': index < 3 }"
                >
                  <td class="position-col">
                    <div
                      :class="[
                        'position',
                        index < 3 ? `position-${index + 1}` : '',
                      ]"
                    >
                      {{ index + 1 }}
                    </div>
                  </td>
                  <td class="player-col">{{ team.name }}</td>
                  <td class="stat-col">{{ team.played || 0 }}</td>
                  <td class="stat-col">{{ team.won || 0 }}</td>
                  <td class="stat-col">{{ team.lost || 0 }}</td>
                  <td class="stat-col points-col">{{ team.points || 0 }}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted, watch } from "vue";
import { collection, getDocs, query, where } from "firebase/firestore";
import { db } from "@/firebase";

export default {
  name: "PublicStandings",

  props: {
    phase: {
      type: String,
      default: "qualification",
      validator: (value) => ["qualification", "division"].includes(value),
    },
  },

  setup(props) {
    const players = ref([]);
    const loading = ref(true);
    const error = ref(null);
    const lastUpdated = ref(null);
    const standingsByDivision = ref([]);
    const standingsByGroup = ref([]);

    // Load team data specific to the current phase
    const loadData = async () => {
      try {
        loading.value = true;
        console.log(`Loading standings for ${props.phase} phase`);

        // Load all teams
        const teamsSnapshot = await getDocs(collection(db, "teams"));
        const allTeams = [];

        teamsSnapshot.forEach((doc) => {
          allTeams.push({
            id: doc.id,
            ...doc.data(),
          });
        });

        console.log(`Loaded ${allTeams.length} teams total`);

        // Filter teams by phase
        if (props.phase === "qualification") {
          // In qualification phase, we want teams that have a groupId (assigned to qualification group)
          players.value = allTeams.filter((team) => team.groupId);
          console.log(
            `Filtered ${players.value.length} teams for qualification phase`
          );
        } else {
          // In division phase, we want teams that have a divisionId
          players.value = allTeams.filter((team) => team.divisionId);
          console.log(
            `Filtered ${players.value.length} teams for division phase`
          );
        }

        // Sort filtered teams by points
        players.value.sort((a, b) => (b.points || 0) - (a.points || 0));

        // Always load the grouped standings
        await loadGroupedStandings();

        lastUpdated.value = new Date().toLocaleString();
        loading.value = false;
      } catch (err) {
        console.error(`Failed to load ${props.phase} standings:`, err);
        error.value = `Failed to load ${props.phase} standings. Please try again later.`;
        loading.value = false;
      }
    };

    // Load grouped standings data
    const loadGroupedStandings = async () => {
      try {
        console.log(`Loading ${props.phase} groups/divisions...`);

        // Query for groups based on phase
        let groupsQuery;

        if (props.phase === "qualification") {
          // Try both 'phase' and 'type' fields for backwards compatibility
          groupsQuery = query(
            collection(db, "groups"),
            where("phase", "in", ["qualification", null])
          );
        } else {
          // For division phase
          groupsQuery = query(
            collection(db, "groups"),
            where("phase", "==", "division")
          );
        }

        const groupsSnapshot = await getDocs(groupsQuery);
        console.log(
          `Found ${groupsSnapshot.size} groups for ${props.phase} phase`
        );

        const groups = {};

        if (groupsSnapshot.size === 0) {
          console.log(`No groups found with phase=${props.phase}`);

          // Try a different approach - for backwards compatibility
          const fallbackQuery = query(
            collection(db, "groups"),
            props.phase === "qualification"
              ? where("type", "==", "qualification")
              : where("type", "in", [
                  "division1",
                  "division2",
                  "division3",
                  "division4",
                ])
          );

          const fallbackSnapshot = await getDocs(fallbackQuery);
          console.log(`Fallback query found ${fallbackSnapshot.size} groups`);

          fallbackSnapshot.forEach((doc) => {
            const groupData = doc.data();
            groups[doc.id] = {
              id: doc.id,
              name: groupData.name || `Group ${doc.id}`,
              order: groupData.order || 0,
              teams: [],
            };
          });
        } else {
          // Use results from first query
          groupsSnapshot.forEach((doc) => {
            const groupData = doc.data();
            groups[doc.id] = {
              id: doc.id,
              name: groupData.name || `Group ${doc.id}`,
              order: groupData.order || 0,
              teams: [],
            };
          });
        }

        // Assign teams to their groups/divisions
        players.value.forEach((team) => {
          const groupId =
            props.phase === "qualification" ? team.groupId : team.divisionId;

          if (groupId && groups[groupId]) {
            groups[groupId].teams.push(team);
          } else {
            console.log(
              `Team ${team.name} (${team.id}) has groupId=${groupId} but no matching group found`
            );
          }
        });

        // Sort teams within each group by points
        Object.values(groups).forEach((group) => {
          group.teams.sort((a, b) => (b.points || 0) - (a.points || 0));
        });

        // Sort groups by their order
        const sortedGroups = Object.values(groups).sort(
          (a, b) => (a.order || 0) - (b.order || 0)
        );

        // Assign to the appropriate reactive property
        if (props.phase === "qualification") {
          standingsByGroup.value = sortedGroups;
          console.log(
            `Set ${sortedGroups.length} qualification groups with teams:`,
            sortedGroups
              .map((g) => `${g.name}: ${g.teams.length} teams`)
              .join(", ")
          );
        } else {
          standingsByDivision.value = sortedGroups;
          console.log(
            `Set ${sortedGroups.length} division groups with teams:`,
            sortedGroups
              .map((g) => `${g.name}: ${g.teams.length} teams`)
              .join(", ")
          );
        }
      } catch (error) {
        console.error("Error loading grouped standings:", error);
      }
    };

    // Watch for changes to the phase prop and reload data when it changes
    watch(
      () => props.phase,
      (newPhase) => {
        console.log(`Phase changed to: ${newPhase}`);
        loadData();
      }
    );

    // Initialize component
    onMounted(() => {
      loadData();
    });

    return {
      players,
      loading,
      error,
      lastUpdated,
      standingsByDivision,
      standingsByGroup,
    };
  },
};
</script>

<style scoped>
.standings-container {
  max-width: 900px;
  margin: 0 auto;
  padding: 0 1rem;
}

h2 {
  color: var(--primary-color);
  margin-bottom: 1.5rem;
  font-weight: 600;
  text-align: center;
  font-size: 1.5rem;
  position: relative;
}

h2::after {
  content: "";
  position: absolute;
  bottom: -8px;
  left: 50%;
  transform: translateX(-50%);
  width: 60px;
  height: 3px;
  background-color: var(--primary-color);
  border-radius: 3px;
}

.no-teams-message {
  background-color: #f9fafb;
  padding: 1.5rem;
  border-radius: 8px;
  text-align: center;
  color: #6b7280;
  margin: 1.25rem 0;
  border: 1px dashed #d1d5db;
}

.standings-table-wrapper {
  overflow-x: auto;
  background-color: white;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
  margin-bottom: 0rem;
  border: 1px solid #e5e7eb;
}

.standings-table {
  width: 100%;
  border-collapse: collapse;
  min-width: 600px;
  margin-bottom: 0rem;
}

.standings-table th {
  background-color: #f8fafc;
  font-weight: 600;
  text-align: left;
  padding: 0.875rem 1rem;
  border-bottom: 2px solid #e5e7eb;
  position: sticky;
  top: 0;
  z-index: 10;
  user-select: none;
  color: #4b5563;
  white-space: nowrap;
}

.standings-table td {
  padding: 0.875rem 1rem;
  border-bottom: 1px solid #f3f4f6;
  color: #1f2937;
  border-radius: 2px;
}

.position-col {
  width: 60px;
  text-align: center;
}

.player-col {
  min-width: 200px;
}

.stat-col {
  width: 80px;
  text-align: center;
}

.points-col {
  font-weight: 600;
  color: #1e40af;
}

.position {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #f3f4f6;
  font-weight: 600;
  color: #4b5563;
  margin: 0 auto;
}

.position-1 {
  background-color: #fcd34d;
  color: #92400e;
  box-shadow: 0 1px 3px rgba(252, 211, 77, 0.5);
}

.position-2 {
  background-color: #e5e7eb;
  color: #374151;
  box-shadow: 0 1px 3px rgba(229, 231, 235, 0.5);
}

.position-3 {
  background-color: #d97706;
  color: white;
  box-shadow: 0 1px 3px rgba(217, 119, 6, 0.5);
}

.top-three {
  background-color: rgba(59, 130, 246, 0.05);
}

.loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2.5rem 0;
  color: #6b7280;
}

.loading-spinner {
  border: 3px solid #f3f4f6;
  border-top: 3px solid #3b82f6;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.error-container {
  background-color: #fee2e2;
  color: #b91c1c;
  padding: 1rem;
  border-radius: 8px;
  text-align: center;
  margin: 1.25rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
  border-left: 4px solid #ef4444;
}

.error-icon {
  background-color: #ef4444;
  color: white;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin-right: 0.625rem;
  font-style: normal;
  font-weight: bold;
}

.last-updated {
  font-size: 0.75rem;
  color: #9ca3af;
  text-align: right;
  margin-top: 1.25rem;
  font-style: italic;
}

.group-standings {
  margin-bottom: 1.875rem;
}

.group-standings h3 {
  background: linear-gradient(135deg, #6366f1, #3b82f6);
  padding: 0.75rem 1rem;
  border-radius: 12px 12px 0 0;
  margin: 0;
  color: white;
  font-size: 1.125rem;
  font-weight: 600;
  border-bottom: none;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@media (max-width: 768px) {
  .form-row {
    flex-direction: column;
    gap: 0.5rem;
  }

  .standings-table th,
  .standings-table td {
    padding: 0.625rem 0.5rem;
    font-size: 0.875rem;
  }

  .position {
    width: 24px;
    height: 24px;
    font-size: 0.75rem;
  }
}
</style>
