<template>
  <div class="team-management">
    <!-- <h2>{{ registrationMode ? "Team Registration" : "Team Management" }}</h2> -->

    <!-- Registration Intro (shown only in registration mode) -->
    <div v-if="registrationMode" class="registration-intro">
      <p>
        Welcome to the team registration for Bedriftsbordtennis Trondheim!
        Please fill out the form below to register your team for the tournament.
      </p>
      <p>
        <strong>Registration deadline:</strong> Make sure to complete your
        registration before the deadline to secure your spot in the tournament.
      </p>
    </div>

    <!-- Team List Section (hidden in registration mode unless admin) -->
    <div v-if="!registrationMode || isAdmin" class="teams-section">
      <div class="section-header">
        <h3>{{ registrationMode ? "Registered Teams" : "Teams" }}</h3>
        <button
          v-if="isAdmin"
          @click="showAddTeamModal = true"
          class="btn add-btn"
        >
          {{ registrationMode ? "Register New Team" : "Add Team" }}
        </button>
      </div>

      <div v-if="loading" class="loading">Loading teams...</div>

      <div v-else-if="error" class="error">
        {{ error }}
      </div>

      <div v-else>
        <div class="filter-section">
          <!-- <div class="form-group">
            <label for="division-filter">Filter by Division:</label>
            <select id="division-filter" v-model="divisionFilter">
              <option value="">All Divisions</option>
              <option
                v-for="division in divisions"
                :key="division.id"
                :value="division.id"
              >
                {{ division.name }}
              </option>
            </select>
          </div> -->

          <div class="form-group">
            <label for="company-filter">Filter by Company:</label>
            <select id="company-filter" v-model="companyFilter">
              <option value="">All Companies</option>
              <option
                v-for="company in uniqueCompanies"
                :key="company"
                :value="company"
              >
                {{ company }}
              </option>
            </select>
          </div>

          <div class="form-group">
            <label for="search-team">Search:</label>
            <input
              type="text"
              id="search-team"
              v-model="searchQuery"
              placeholder="Search teams..."
            />
          </div>
        </div>

        <!-- Teams Table -->
        <div class="teams-table-container">
          <table class="teams-table">
            <thead>
              <tr>
                <th class="row-num-col">#</th>
                <th>Team Name</th>
                <th>Company</th>
                <th>Members</th>
                <!-- <th>Total</th> -->
                <!-- <th>Division</th> -->
                <th v-if="isAdmin" class="action-column">Actions</th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(team, index) in filteredTeams"
                :key="team.id"
                class="team-row"
              >
                <td class="row-num-cell">{{ index + 1 }}</td>
                <td class="team-name-cell">
                  <div class="team-name">{{ team.name }}</div>
                </td>
                <td>{{ team.company || "-" }}</td>
                <td>
                  <div class="members-list">
                    <div v-if="team.members && team.members.length">
                      <div
                        v-for="(member, idx) in team.members"
                        :key="idx"
                        :class="{
                          'captain-member':
                            team.contactInfo &&
                            team.contactInfo.name === getFullName(member),
                        }"
                      >
                        <span class="member-name"
                          >{{ idx + 1 }}. {{ getFullName(member) }}</span
                        >
                        <span
                          v-if="
                            team.contactInfo &&
                            team.contactInfo.name === getFullName(member)
                          "
                          class="captain-indicator"
                          >✉️</span
                        >
                      </div>
                    </div>
                    <span v-else>-</span>
                  </div>
                </td>
                <!-- <td class="total-members-cell">
                  {{ team.members ? team.members.length : 0 }}
                </td> -->
                <!-- <td>{{ getDivisionName(team.divisionId) }}</td> -->
                <td v-if="isAdmin" class="action-cell">
                  <div class="team-actions">
                    <button @click="editTeam(team)" class="btn edit-btn">
                      Edit
                    </button>
                    <button
                      @click="confirmDeleteTeam(team)"
                      class="btn delete-btn"
                    >
                      Delete
                    </button>
                  </div>
                </td>
              </tr>
            </tbody>
            <tfoot>
              <tr class="total-row">
                <td colspan="3" class="total-row-label">Total Members</td>
                <td class="total-members-sum">
                  {{ getTotalMembersCount }}
                </td>
                <td v-if="isAdmin"></td>
              </tr>
            </tfoot>
          </table>

          <div v-if="filteredTeams.length === 0" class="no-data">
            No teams found matching the current filters.
          </div>
        </div>
      </div>
    </div>

    <!-- Registration Form (shown to public in registration mode) -->
    <div
      v-if="registrationMode && !isAdmin"
      class="registration-form-container"
    >
      <div class="registration-form">
        <h3>Register Your Team</h3>

        <div class="form-group">
          <label for="reg-team-name"
            >Team Name: <span class="required">*</span></label
          >
          <input
            type="text"
            id="reg-team-name"
            v-model="registration.name"
            placeholder="Enter team name"
            required
          />
        </div>

        <div class="form-group">
          <label for="reg-company"
            >Company/Organization: <span class="required">*</span></label
          >
          <div class="company-select-container">
            <select
              id="reg-company"
              v-model="registration.company"
              v-if="!isNewCompanyReg"
              class="company-select"
              required
            >
              <option value="">-- Select Company --</option>
              <option
                v-for="company in uniqueCompanies"
                :key="company"
                :value="company"
              >
                {{ company }}
              </option>
              <option value="__add_new__">+ Add New Company</option>
            </select>
            <input
              v-if="isNewCompanyReg"
              type="text"
              v-model="registration.company"
              placeholder="Enter new company name"
              class="company-input"
              required
            />
            <button
              v-if="isNewCompanyReg"
              @click="cancelAddCompanyReg"
              class="btn cancel-new-btn"
            >
              Cancel
            </button>
          </div>
        </div>

        <h4>Team Members</h4>
        <p class="helper-text">
          Please add all players who will participate in the tournament
        </p>

        <div class="members-container">
          <div
            v-for="(member, index) in registration.members"
            :key="index"
            class="member-row"
          >
            <div class="member-name-inputs">
              <input
                type="text"
                v-model="member.firstName"
                placeholder="First name"
                @input="autoAddRegistrationMember(index)"
                class="member-input first-name"
              />
              <input
                type="text"
                v-model="member.lastName"
                placeholder="Last name"
                class="member-input last-name"
              />
            </div>
            <button
              v-if="
                registration.members.length > 1 &&
                index < registration.members.length - 1
              "
              @click="removeRegistrationMember(index)"
              class="btn remove-btn"
            >
              Remove
            </button>
          </div>
        </div>

        <h4>Contact Information</h4>

        <div class="form-group">
          <label for="reg-contact-name"
            >Team Captain: <span class="required">*</span></label
          >
          <select
            id="reg-contact-name"
            v-model="registration.contactInfo.memberId"
            required
            class="captain-select"
          >
            <option value="">-- Select Team Captain --</option>
            <option
              v-for="(member, index) in registration.members.filter((m) =>
                m.firstName.trim()
              )"
              :key="index"
              :value="index"
            >
              {{ getFullName(member) }}
            </option>
          </select>
        </div>

        <div class="form-row">
          <div class="form-group">
            <label for="reg-contact-email"
              >Email: <span class="required">*</span></label
            >
            <input
              type="email"
              id="reg-contact-email"
              v-model="registration.contactInfo.email"
              placeholder="Contact email"
              required
            />
          </div>

          <div class="form-group">
            <label for="reg-contact-phone">Phone Number:</label>
            <input
              type="tel"
              id="reg-contact-phone"
              v-model="registration.contactInfo.phone"
              placeholder="Contact phone number"
            />
          </div>
        </div>

        <h4>Team Preferences</h4>

        <div class="form-row">
          <div class="form-group">
            <label for="reg-preferred-day"
              >Preferred Match Day: <span class="required">*</span></label
            >
            <select
              id="reg-preferred-day"
              v-model="registration.preferredDay"
              required
            >
              <option value="">-- Select day --</option>
              <option value="Monday">Monday</option>
              <option value="Wednesday">Wednesday</option>
              <option value="Both">Both days work</option>
              <option value="Monday Only">Monday Only</option>
              <option value="Wednesday Only">Wednesday Only</option>
            </select>
          </div>

          <div class="form-group">
            <label for="reg-preferred-time"
              >Preferred Match Time: <span class="required">*</span></label
            >
            <select
              id="reg-preferred-time"
              v-model="registration.preferredTime"
              required
            >
              <option value="">-- Select time --</option>
              <option value="20:00">20:00</option>
              <option value="21:00">21:00</option>
            </select>
          </div>
        </div>

        <div class="form-group">
          <label for="reg-comments">Additional Comments:</label>
          <textarea
            id="reg-comments"
            v-model="registration.comments"
            placeholder="Any additional information or special requests"
            rows="3"
          ></textarea>
        </div>

        <div class="form-actions">
          <button
            @click="submitRegistration"
            class="btn register-btn"
            :disabled="isSubmitting"
          >
            {{ isSubmitting ? "Submitting..." : "Submit Registration" }}
          </button>
          <button @click="clearRegistration" class="btn clear-btn">
            Clear Form
          </button>
        </div>

        <div
          v-if="registrationMessage"
          class="registration-message"
          :class="registrationStatus"
        >
          {{ registrationMessage }}
        </div>
      </div>
    </div>

    <!-- Add/Edit Team Modal -->
    <div v-if="showAddTeamModal || showEditTeamModal" class="modal-overlay">
      <div class="modal-content">
        <div class="modal-header">
          <h3>{{ showEditTeamModal ? "Edit Team" : "Add New Team" }}</h3>
          <button @click="closeModal" class="close-btn">&times;</button>
        </div>
        <div class="modal-body">
          <div class="form-group">
            <label for="team-name"
              >Team Name: <span class="required">*</span></label
            >
            <input
              type="text"
              id="team-name"
              v-model="editingTeam.name"
              placeholder="Enter team name"
              required
            />
          </div>

          <div class="form-group">
            <label for="team-company">Company/Organization:</label>
            <div class="company-select-container">
              <select
                id="team-company"
                v-model="editingTeam.company"
                v-if="!isNewCompany"
                class="company-select"
              >
                <option value="">-- Select Company --</option>
                <option
                  v-for="company in uniqueCompanies"
                  :key="company"
                  :value="company"
                >
                  {{ company }}
                </option>
                <option value="__add_new__">+ Add New Company</option>
              </select>
              <input
                v-if="isNewCompany"
                type="text"
                v-model="editingTeam.company"
                placeholder="Enter new company name"
                class="company-input"
              />
              <button
                v-if="isNewCompany"
                @click="cancelAddCompany"
                class="btn cancel-new-btn"
              >
                Cancel
              </button>
            </div>
          </div>

          <!-- Team Members Section - Moved up after Company -->
          <div class="team-members-section">
            <h4>Team Members</h4>
            <div class="members-container">
              <div
                v-for="(member, index) in editingTeam.members"
                :key="index"
                class="member-row"
              >
                <div class="member-name-inputs">
                  <input
                    type="text"
                    v-model="member.firstName"
                    placeholder="First name"
                    @input="autoAddMemberField(index)"
                    class="member-input first-name"
                  />
                  <input
                    type="text"
                    v-model="member.lastName"
                    placeholder="Last name"
                    class="member-input last-name"
                  />
                </div>
                <button
                  v-if="
                    editingTeam.members.length > 1 &&
                    (index < editingTeam.members.length - 1 ||
                      member.firstName.trim())
                  "
                  @click="removeEditingMember(index)"
                  class="btn remove-btn"
                >
                  Remove
                </button>
              </div>
            </div>
          </div>

          <div class="contact-info">
            <h4>Contact Information</h4>
            <!-- Contact Name dropdown from team members -->
            <div class="form-group">
              <label for="contact-member">Team Captain:</label>
              <select
                id="contact-member"
                v-model="editingTeam.contactInfo.memberId"
                class="captain-select"
              >
                <option value="">-- Select Team Captain --</option>
                <option
                  v-for="(member, index) in editingTeam.members.filter((m) =>
                    m.firstName.trim()
                  )"
                  :key="index"
                  :value="index"
                >
                  {{ getFullName(member) }}
                </option>
              </select>
            </div>

            <div class="form-row">
              <div class="form-group">
                <label for="contact-email">Email:</label>
                <input
                  type="email"
                  id="contact-email"
                  v-model="editingTeam.contactInfo.email"
                  placeholder="Contact email"
                />
              </div>

              <div class="form-group">
                <label for="contact-phone">Phone Number:</label>
                <input
                  type="tel"
                  id="contact-phone"
                  v-model="editingTeam.contactInfo.phone"
                  placeholder="Contact phone number"
                />
              </div>
            </div>
          </div>

          <div class="form-row">
            <div class="form-group">
              <label for="team-preferred-day">Preferred Match Day:</label>
              <select
                id="team-preferred-day"
                v-model="editingTeam.preferredDay"
              >
                <option value="">-- Select day --</option>
                <option value="Monday">Monday</option>
                <option value="Wednesday">Wednesday</option>
                <option value="Both">Both days work</option>
                <option value="Monday Only">Monday Only</option>
                <option value="Wednesday Only">Wednesday Only</option>
              </select>
            </div>

            <div class="form-group">
              <label for="team-preferred-time">Preferred Match Time:</label>
              <select
                id="team-preferred-time"
                v-model="editingTeam.preferredTime"
              >
                <option value="">-- Select time --</option>
                <option value="20:00">20:00</option>
                <option value="21:00">21:00</option>
              </select>
            </div>
          </div>

          <div class="form-row">
            <div class="form-group">
              <label for="team-group">Qualification Group:</label>
              <select id="team-group" v-model="editingTeam.groupId">
                <option value="">-- Select Qualification Group --</option>
                <option
                  v-for="group in groups"
                  :key="group.id"
                  :value="group.id"
                >
                  {{ group.name }}
                </option>
              </select>
            </div>
            <div class="form-group">
              <label for="team-division">Division:</label>
              <select id="team-division" v-model="editingTeam.divisionId">
                <option value="">-- Select Division --</option>
                <option
                  v-for="division in divisions"
                  :key="division.id"
                  :value="division.id"
                >
                  {{ division.name }}
                </option>
              </select>
            </div>
          </div>

          <div class="form-group">
            <label for="team-comments">Additional Comments:</label>
            <textarea
              id="team-comments"
              v-model="editingTeam.comments"
              placeholder="Any additional information or special requests"
              rows="3"
            ></textarea>
          </div>
        </div>
        <div class="modal-footer">
          <button @click="saveTeam" class="btn save-btn">
            {{ showEditTeamModal ? "Update Team" : "Save Team" }}
          </button>
          <button @click="closeModal" class="btn cancel-btn">Cancel</button>
        </div>
      </div>
    </div>

    <!-- Delete Confirmation Modal -->
    <div v-if="showDeleteModal" class="modal-overlay">
      <div class="modal-content">
        <div class="modal-header">
          <h3>Confirm Delete</h3>
          <button @click="showDeleteModal = false" class="close-btn">
            &times;
          </button>
        </div>
        <div class="modal-body">
          <p>
            Are you sure you want to delete team:
            <strong>{{ teamToDelete?.name }}</strong
            >?
          </p>
          <p class="warning">
            This action cannot be undone. All related matches will remain but
            will be marked as involving a deleted team.
          </p>
        </div>
        <div class="modal-footer">
          <button @click="deleteTeam" class="btn delete-btn">Delete</button>
          <button @click="showDeleteModal = false" class="btn cancel-btn">
            Cancel
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted, reactive, watch } from "vue";
import {
  collection,
  getDocs,
  doc,
  updateDoc,
  deleteDoc,
  addDoc,
  query,
  orderBy,
  serverTimestamp,
} from "firebase/firestore";
import { db } from "@/firebase";

export default {
  name: "TeamManagement",

  props: {
    isAdmin: {
      type: Boolean,
      default: false,
    },
    registrationMode: {
      type: Boolean,
      default: false,
    },
  },

  // eslint-disable-next-line no-unused-vars
  setup(props) {
    // Data state
    const teams = ref([]);
    const divisions = ref([]);
    const groups = ref([]);
    const loading = ref(true);
    const error = ref(null);

    // Filters
    const divisionFilter = ref("");
    const companyFilter = ref("");
    const searchQuery = ref("");

    // Company handling
    const isNewCompany = ref(false);
    const isNewCompanyReg = ref(false);

    // Modals state
    const showAddTeamModal = ref(false);
    const showEditTeamModal = ref(false);
    const showDeleteModal = ref(false);

    // Form state for both edit and add
    const editingTeam = reactive({
      id: "",
      name: "",
      company: "",
      preferredDay: "",
      preferredTime: "",
      divisionId: "",
      groupId: "",
      members: [],
      contactInfo: {
        name: "",
        email: "",
        phone: "",
        memberId: "", // Added memberId to track selected team member
      },
      comments: "",
    });

    const teamToDelete = ref(null);

    // Public registration form
    const registration = reactive({
      name: "",
      company: "",
      preferredDay: "",
      preferredTime: "",
      members: [],
      contactInfo: {
        name: "",
        email: "",
        phone: "",
        memberId: "", // Added memberId to track selected team member
      },
      comments: "",
    });

    const isSubmitting = ref(false);
    const registrationMessage = ref("");
    const registrationStatus = ref("");

    // Helper function to get full name
    const getFullName = (member) => {
      if (!member) return "";

      // If we're dealing with legacy member data that only has name
      if (member.name !== undefined && !member.firstName) {
        return member.name;
      }

      // With first and last name structure
      const firstName = member.firstName || "";
      const lastName = member.lastName || "";

      if (firstName && lastName) {
        return `${firstName} ${lastName}`;
      } else {
        return firstName || lastName;
      }
    };

    // Watch for changes in contact memberId and update the name
    watch(
      () => editingTeam.contactInfo.memberId,
      (newVal) => {
        if (newVal !== "" && editingTeam.members[newVal]) {
          editingTeam.contactInfo.name = getFullName(
            editingTeam.members[newVal]
          );
        }
      }
    );

    // Watch for changes in registration contact memberId and update the name
    watch(
      () => registration.contactInfo.memberId,
      (newVal) => {
        if (newVal !== "" && registration.members[newVal]) {
          registration.contactInfo.name = getFullName(
            registration.members[newVal]
          );
        }
      }
    );

    // Watch for company selection for adding new company
    watch(
      () => editingTeam.company,
      (newVal) => {
        if (newVal === "__add_new__") {
          editingTeam.company = "";
          isNewCompany.value = true;
        }
      }
    );

    // Watch for company selection in registration
    watch(
      () => registration.company,
      (newVal) => {
        if (newVal === "__add_new__") {
          registration.company = "";
          isNewCompanyReg.value = true;
        }
      }
    );

    // Computed properties
    const filteredTeams = computed(() => {
      let result = teams.value;

      // Apply division filter
      if (divisionFilter.value) {
        result = result.filter(
          (team) => team.divisionId === divisionFilter.value
        );
      }

      // Apply company filter
      if (companyFilter.value) {
        result = result.filter((team) => team.company === companyFilter.value);
      }

      // Apply search filter
      if (searchQuery.value) {
        const query = searchQuery.value.toLowerCase();
        result = result.filter(
          (team) =>
            team.name.toLowerCase().includes(query) ||
            (team.company && team.company.toLowerCase().includes(query)) ||
            (team.members &&
              team.members.some((member) => {
                const fullName = getFullName(member).toLowerCase();
                return fullName.includes(query);
              }))
        );
      }

      return result;
    });

    // Get unique companies from all teams
    const uniqueCompanies = computed(() => {
      const companies = teams.value
        .map((team) => team.company)
        .filter((company) => company && company.trim() !== "");

      return [...new Set(companies)].sort();
    });

    // Calculate total members across all filtered teams
    const getTotalMembersCount = computed(() => {
      return filteredTeams.value.reduce((total, team) => {
        return total + (team.members ? team.members.length : 0);
      }, 0);
    });

    // Methods
    const loadData = async () => {
      try {
        loading.value = true;

        // Load teams, divisions and groups
        const teamsQuery = query(collection(db, "teams"), orderBy("name"));
        const divisionsQuery = query(
          collection(db, "divisions"),
          orderBy("order")
        );
        const groupsQuery = query(collection(db, "groups"), orderBy("order"));

        const [teamsSnapshot, divisionsSnapshot, groupsSnapshot] =
          await Promise.all([
            getDocs(teamsQuery),
            getDocs(divisionsQuery),
            getDocs(groupsQuery),
          ]);

        teams.value = teamsSnapshot.docs.map((doc) => ({
          id: doc.id,
          ...doc.data(),
        }));

        divisions.value = divisionsSnapshot.docs.map((doc) => ({
          id: doc.id,
          ...doc.data(),
        }));

        groups.value = groupsSnapshot.docs.map((doc) => ({
          id: doc.id,
          ...doc.data(),
        }));

        loading.value = false;
      } catch (err) {
        error.value = "Failed to load data. Please try again.";
        loading.value = false;
        console.error(err);
      }
    };

    const getDivisionName = (divisionId) => {
      if (!divisionId) return "None";
      const division = divisions.value.find((d) => d.id === divisionId);
      return division ? division.name : "Unknown";
    };

    const getGroupName = (groupId) => {
      if (!groupId) return "None";
      const group = groups.value.find((g) => g.id === groupId);
      return group ? group.name : "Unknown";
    };

    // Team member management for new/edit team
    const addEditingMember = () => {
      editingTeam.members.push({ firstName: "", lastName: "" });
    };

    // Auto-add member field when typing in the last field
    const autoAddMemberField = (index) => {
      // If this is the last field and it has text, add a new empty field
      if (
        index === editingTeam.members.length - 1 &&
        editingTeam.members[index].firstName.trim()
      ) {
        addEditingMember();
      }
    };

    const removeEditingMember = (index) => {
      // Only remove if there will be at least one member field left
      if (editingTeam.members.length > 1) {
        // If we're removing the member who is the captain, clear the captain field
        if (editingTeam.contactInfo.memberId === index.toString()) {
          editingTeam.contactInfo.memberId = "";
          editingTeam.contactInfo.name = "";
        }
        // If we're removing a member before the captain, update the captain's index
        else if (editingTeam.contactInfo.memberId > index) {
          editingTeam.contactInfo.memberId = (
            parseInt(editingTeam.contactInfo.memberId) - 1
          ).toString();
        }
        editingTeam.members.splice(index, 1);
      }
    };

    // Team member management for registration
    const addRegistrationMember = () => {
      registration.members.push({ firstName: "", lastName: "" });
    };

    // Auto-add registration member field
    const autoAddRegistrationMember = (index) => {
      if (
        index === registration.members.length - 1 &&
        registration.members[index].firstName.trim()
      ) {
        addRegistrationMember();
      }
    };

    const removeRegistrationMember = (index) => {
      // Only remove if there will be at least one member field left
      if (registration.members.length > 1) {
        // If we're removing the member who is the captain, clear the captain field
        if (registration.contactInfo.memberId === index.toString()) {
          registration.contactInfo.memberId = "";
          registration.contactInfo.name = "";
        }
        // If we're removing a member before the captain, update the captain's index
        else if (registration.contactInfo.memberId > index) {
          registration.contactInfo.memberId = (
            parseInt(registration.contactInfo.memberId) - 1
          ).toString();
        }
        registration.members.splice(index, 1);
      }
    };

    // Reset and close modals
    const closeModal = () => {
      showAddTeamModal.value = false;
      showEditTeamModal.value = false;
      isNewCompany.value = false;
      resetEditingTeam();
    };

    const resetEditingTeam = () => {
      editingTeam.id = "";
      editingTeam.name = "";
      editingTeam.company = "";
      editingTeam.preferredDay = "";
      editingTeam.preferredTime = "";
      editingTeam.divisionId = "";
      editingTeam.groupId = "";
      editingTeam.members = [{ firstName: "", lastName: "" }]; // Always start with one empty member
      editingTeam.contactInfo = {
        name: "",
        email: "",
        phone: "",
        memberId: "", // Added memberId reset
      };
      editingTeam.comments = "";
    };

    const cancelAddCompany = () => {
      isNewCompany.value = false;
      editingTeam.company = "";
    };

    const cancelAddCompanyReg = () => {
      isNewCompanyReg.value = false;
      registration.company = "";
    };

    // Clear registration form
    const clearRegistration = () => {
      registration.name = "";
      registration.company = "";
      registration.preferredDay = "";
      registration.preferredTime = "";
      registration.members = [{ firstName: "", lastName: "" }]; // Always have one empty member
      registration.contactInfo = {
        name: "",
        email: "",
        phone: "",
        memberId: "", // Added memberId reset
      };
      registration.comments = "";
      isNewCompanyReg.value = false;
      registrationMessage.value = "";
    };

    // Team CRUD operations
    const saveTeam = async () => {
      if (!editingTeam.name) {
        alert("Team name is required");
        return;
      }

      try {
        // Filter out empty member names
        const validMembers = editingTeam.members.filter(
          (member) => member.firstName.trim() !== ""
        );

        // Make sure we have the contact name from the selected member
        if (editingTeam.contactInfo.memberId !== "") {
          const selectedMember =
            editingTeam.members[editingTeam.contactInfo.memberId];
          if (selectedMember) {
            editingTeam.contactInfo.name = getFullName(selectedMember);
          }
        }

        const teamData = {
          name: editingTeam.name,
          company: editingTeam.company || null,
          preferredDay: editingTeam.preferredDay || null,
          preferredTime: editingTeam.preferredTime || null,
          divisionId: editingTeam.divisionId || null,
          groupId: editingTeam.groupId || null,
          members: validMembers,
          contactInfo: {
            name: editingTeam.contactInfo.name || "",
            email: editingTeam.contactInfo.email || "",
            phone: editingTeam.contactInfo.phone || "",
            memberId: editingTeam.contactInfo.memberId || "",
          },
          comments: editingTeam.comments || "",
          updatedAt: serverTimestamp(),
        };

        if (showEditTeamModal.value) {
          // Update existing team
          await updateDoc(doc(db, "teams", editingTeam.id), teamData);
          // alert("Team updated successfully!");
        } else {
          // Add new team
          teamData.createdAt = serverTimestamp();
          teamData.played = 0;
          teamData.won = 0;
          teamData.lost = 0;
          teamData.points = 0;

          await addDoc(collection(db, "teams"), teamData);
          // alert("Team added successfully!");
        }

        // Close modal and reload data
        closeModal();
        await loadData();
      } catch (err) {
        alert("Error saving team. Please try again.");
        console.error(err);
      }
    };

    const editTeam = (team) => {
      // Clone team data to avoid direct modification
      editingTeam.id = team.id;
      editingTeam.name = team.name;
      editingTeam.company = team.company || "";
      editingTeam.preferredDay = team.preferredDay || "";
      editingTeam.preferredTime = team.preferredTime || "";
      editingTeam.divisionId = team.divisionId || "";
      editingTeam.groupId = team.groupId || "";

      // Handle different member data structures (legacy vs new)
      if (team.members && team.members.length) {
        editingTeam.members = team.members.map((member) => {
          // Handle legacy data format
          if (member.name !== undefined && !member.firstName) {
            return {
              firstName: member.name,
              lastName: "",
            };
          } else {
            // Handle new data format or ensure structure is correct
            return {
              firstName: member.firstName || "",
              lastName: member.lastName || "",
            };
          }
        });
      } else {
        // Ensure we have at least one empty member
        editingTeam.members = [{ firstName: "", lastName: "" }];
      }

      // Set contact info including memberId
      if (team.contactInfo) {
        editingTeam.contactInfo.name = team.contactInfo.name || "";
        editingTeam.contactInfo.email = team.contactInfo.email || "";
        editingTeam.contactInfo.phone = team.contactInfo.phone || "";
        editingTeam.contactInfo.memberId = team.contactInfo.memberId || "";

        // If memberId is not set but we have a contact name, try to find the matching member
        if (!editingTeam.contactInfo.memberId && editingTeam.contactInfo.name) {
          const memberIndex = editingTeam.members.findIndex(
            (m) => getFullName(m) === editingTeam.contactInfo.name
          );
          if (memberIndex >= 0) {
            editingTeam.contactInfo.memberId = memberIndex.toString();
          }
        }
      } else {
        editingTeam.contactInfo = {
          name: "",
          email: "",
          phone: "",
          memberId: "",
        };
      }
      editingTeam.comments = team.comments || "";

      showEditTeamModal.value = true;
    };

    const confirmDeleteTeam = (team) => {
      teamToDelete.value = team;
      showDeleteModal.value = true;
    };

    const deleteTeam = async () => {
      if (!teamToDelete.value) return;

      try {
        await deleteDoc(doc(db, "teams", teamToDelete.value.id));

        // Close modal and reload data
        showDeleteModal.value = false;
        teamToDelete.value = null;
        await loadData();

        // alert("Team deleted successfully!");
      } catch (err) {
        alert("Error deleting team. Please try again.");
        console.error(err);
      }
    };

    // Submit public registration
    const submitRegistration = async () => {
      // Basic validation
      if (
        !registration.name ||
        !registration.company ||
        !registration.preferredDay ||
        !registration.preferredTime ||
        !registration.contactInfo.memberId || // Changed to check memberId
        !registration.contactInfo.email
      ) {
        registrationMessage.value = "Please fill in all required fields.";
        registrationStatus.value = "error";
        return;
      }

      // Email validation
      const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      if (!emailRegex.test(registration.contactInfo.email)) {
        registrationMessage.value = "Please enter a valid email address.";
        registrationStatus.value = "error";
        return;
      }

      try {
        isSubmitting.value = true;

        // Filter out empty member names
        const validMembers = registration.members.filter(
          (member) => member.firstName.trim() !== ""
        );

        // Make sure we have the contact name from the selected member
        if (registration.contactInfo.memberId !== "") {
          const selectedMember =
            registration.members[registration.contactInfo.memberId];
          if (selectedMember) {
            registration.contactInfo.name = getFullName(selectedMember);
          }
        }

        const teamData = {
          name: registration.name,
          company: registration.company,
          preferredDay: registration.preferredDay,
          preferredTime: registration.preferredTime,
          members: validMembers,
          contactInfo: {
            name: registration.contactInfo.name || "",
            email: registration.contactInfo.email || "",
            phone: registration.contactInfo.phone || "",
            memberId: registration.contactInfo.memberId || "",
          },
          comments: registration.comments,
          createdAt: serverTimestamp(),
          // Initialize stats for future use
          played: 0,
          won: 0,
          lost: 0,
          points: 0,
        };

        await addDoc(collection(db, "teams"), teamData);

        // Success
        registrationMessage.value =
          "Team registration submitted successfully! You will receive a confirmation email.";
        registrationStatus.value = "success";

        // Clear form after success
        clearRegistration();
      } catch (err) {
        registrationMessage.value =
          "Error submitting registration. Please try again.";
        registrationStatus.value = "error";
        console.error(err);
      } finally {
        isSubmitting.value = false;
      }
    };

    // Initialize component
    onMounted(() => {
      loadData();

      // Initialize registration form with one empty member
      if (registration.members.length === 0) {
        registration.members.push({ firstName: "", lastName: "" });
      }

      // Initialize editing team with one empty member if needed
      if (editingTeam.members.length === 0) {
        editingTeam.members.push({ firstName: "", lastName: "" });
      }
    });

    return {
      teams,
      divisions,
      groups,
      loading,
      error,
      divisionFilter,
      companyFilter,
      searchQuery,
      filteredTeams,
      uniqueCompanies,
      isNewCompany,
      isNewCompanyReg,
      showAddTeamModal,
      showEditTeamModal,
      showDeleteModal,
      editingTeam,
      teamToDelete,
      registration,
      isSubmitting,
      registrationMessage,
      registrationStatus,
      getDivisionName,
      getGroupName,
      getTotalMembersCount,
      getFullName,
      addEditingMember,
      removeEditingMember,
      autoAddMemberField,
      addRegistrationMember,
      removeRegistrationMember,
      autoAddRegistrationMember,
      saveTeam,
      editTeam,
      closeModal,
      confirmDeleteTeam,
      deleteTeam,
      submitRegistration,
      clearRegistration,
      cancelAddCompany,
      cancelAddCompanyReg,
    };
  },
};
</script>

