import Vue from "vue";
import VueRouter from "vue-router";
// import HomePage from "../views/HomePage.vue";
import config from "../config.json"; // Ensure this path matches your config location
import TableTennisView from "../views/TableTennisView.vue";
Vue.use(VueRouter);

const routes = [
  // {
  //   path: "/",
  //   name: "HomePage",
  //   component: HomePage,
  //   meta: {
  //     title: "Home",
  //   },
  // },
  {
    path: "/",
    name: "TableTennis",
    component: TableTennisView,
  },
];

const router = new VueRouter({
  mode: "history",
  base: "/",
  routes,
});

router.afterEach((to) => {
  // Get app name from your config if available
  const appName = config.appInfo?.name || "TBTK";

  // Update document title
  if (to.meta.title) {
    document.title = `${to.meta.title}`;
  } else {
    document.title = appName;
  }
});

export default router;
